
  import '/Users/dupeiyi/Project/private/x/packages/x/components/conversations/demo/controlled-mode.tsx?watch=parent';
  export default "import {\n  CodeOutlined,\n  FileImageOutlined,\n  FileSearchOutlined,\n  SignatureOutlined,\n} from '@ant-design/icons';\nimport { Conversations, type ConversationsProps } from '@ant-design/x';\nimport type { GetProp } from 'antd';\nimport { Button, Flex, theme } from 'antd';\nimport React, { useState } from 'react';\n\nconst items: GetProp<ConversationsProps, 'items'> = [\n  {\n    key: 'write',\n    label: 'Help Me Write',\n    icon: <SignatureOutlined />,\n  },\n  {\n    key: 'coding',\n    label: 'AI Coding',\n    icon: <CodeOutlined />,\n  },\n  {\n    key: 'createImage',\n    label: 'Create Image',\n    icon: <FileImageOutlined />,\n  },\n  {\n    key: 'deepSearch',\n    label: 'Deep Search',\n    icon: <FileSearchOutlined />,\n  },\n];\n\nconst App: React.FC = () => {\n  const [activeKey, setActiveKey] = useState<string>('item1');\n\n  const { token } = theme.useToken();\n\n  // Customize the style of the container\n  const style = {\n    width: 256,\n    background: token.colorBgContainer,\n    borderRadius: token.borderRadius,\n  };\n\n  return (\n    <Flex vertical gap=\"small\" align=\"flex-start\">\n      <Conversations\n        activeKey={activeKey}\n        onActiveChange={(v) => {\n          setActiveKey(v);\n        }}\n        items={items}\n        style={style}\n      />\n\n      <Flex gap=\"small\">\n        <Button\n          onClick={() => {\n            setActiveKey('write');\n          }}\n        >\n          Active First\n        </Button>\n        <Button\n          onClick={() => {\n            setActiveKey('deepSearch');\n          }}\n        >\n          Active Last\n        </Button>\n      </Flex>\n    </Flex>\n  );\n};\n\nexport default App;\n";;
  