
  import '/Users/dupeiyi/Project/private/x/packages/x/components/sender/demo/slot-with-suggestion.tsx?watch=parent';
  export default "import {\n  AntDesignOutlined,\n  ApiOutlined,\n  CodeOutlined,\n  EditOutlined,\n  FileImageOutlined,\n  OpenAIFilled,\n  OpenAIOutlined,\n  PaperClipOutlined,\n  ProfileOutlined,\n  SearchOutlined,\n} from '@ant-design/icons';\nimport { Attachments, AttachmentsProps, Sender, SenderProps, Suggestion } from '@ant-design/x';\nimport { Button, Divider, Dropdown, Flex, GetProp, GetRef, MenuProps, message } from 'antd';\nimport React, { useEffect, useRef, useState } from 'react';\n\nconst Switch = Sender.Switch;\n\nconst AgentInfo: {\n  [key: string]: {\n    icon: React.ReactNode;\n    label: string;\n    slotConfig: SenderProps['slotConfig'];\n  };\n} = {\n  deep_search: {\n    icon: <SearchOutlined />,\n    label: 'Deep Search',\n    slotConfig: [\n      { type: 'text', value: 'Please help me search for news about ' },\n      {\n        type: 'select',\n        key: 'search_type',\n        props: {\n          options: ['AI', 'Technology', 'Entertainment'],\n          placeholder: 'Please select a category',\n        },\n      },\n      { type: 'text', value: ' and summarize it into a list.' },\n    ],\n  },\n  ai_code: {\n    icon: <CodeOutlined />,\n    label: 'AI Code',\n    slotConfig: [\n      { type: 'text', value: 'Please use ' },\n      {\n        type: 'select',\n        key: 'code_lang',\n        props: {\n          options: ['JS', 'C++', 'Java'],\n          placeholder: 'Please select a programming language',\n        },\n      },\n      { type: 'text', value: ' to write a mini game.' },\n    ],\n  },\n  ai_writing: {\n    icon: <EditOutlined />,\n    label: 'Writing',\n    slotConfig: [\n      { type: 'text', value: 'Please write an article about ' },\n      {\n        type: 'select',\n        key: 'writing_type',\n        props: {\n          options: ['Campus', 'Travel', 'Reading'],\n          placeholder: 'Please enter a topic',\n        },\n      },\n      { type: 'text', value: '. The requirement is ' },\n      {\n        type: 'input',\n        key: 'writing_num',\n        props: {\n          defaultValue: '800',\n          placeholder: 'Please enter the number of words.',\n        },\n      },\n      { type: 'text', value: ' words.' },\n    ],\n  },\n};\n\nconst IconStyle = {\n  fontSize: 16,\n};\n\nconst SwitchTextStyle = {\n  display: 'inline-flex',\n  width: 28,\n  justifyContent: 'center',\n  alignItems: 'center',\n};\n\nconst FileInfo: {\n  [key: string]: {\n    icon: React.ReactNode;\n    label: string;\n  };\n} = {\n  file_image: {\n    icon: <FileImageOutlined />,\n    label: 'x-image',\n  },\n};\n\nconst App: React.FC = () => {\n  const [loading, setLoading] = useState<boolean>(false);\n  type SuggestionItems = Exclude<GetProp<typeof Suggestion, 'items'>, () => void>;\n  const [deepThink, setDeepThink] = useState<boolean>(true);\n  const [activeAgentKey, setActiveAgentKey] = useState('deep_search');\n  const [fileList, setFileList] = useState<AttachmentsProps['items']>([]);\n  const agentItems: MenuProps['items'] = Object.keys(AgentInfo).map((agent) => {\n    const { icon, label } = AgentInfo[agent];\n    return {\n      key: agent,\n      icon,\n      label,\n    };\n  });\n  const [open, setOpen] = React.useState(false);\n\n  const attachmentsRef = React.useRef<GetRef<typeof Attachments>>(null);\n  const fileItems = Object.keys(FileInfo).map((file) => {\n    const { icon, label } = FileInfo[file];\n    return {\n      key: file,\n      icon,\n      label,\n    };\n  });\n\n  const senderRef = useRef<GetRef<typeof Sender>>(null);\n\n  const agentItemClick: MenuProps['onClick'] = (item) => {\n    setActiveAgentKey(item.key);\n  };\n  const fileItemClick: MenuProps['onClick'] = (item) => {\n    const { icon, label } = FileInfo[item.key];\n    senderRef.current?.insert?.([\n      {\n        type: 'tag',\n        key: `${item.key}_${Date.now()}`,\n        props: {\n          label: (\n            <Flex gap=\"small\">\n              {icon}\n              {label}\n            </Flex>\n          ),\n          value: item.key,\n        },\n      },\n    ]);\n  };\n\n  // Mock send message\n  useEffect(() => {\n    if (loading) {\n      const timer = setTimeout(() => {\n        setLoading(false);\n        message.success('Send message successfully!');\n      }, 3000);\n      return () => {\n        clearTimeout(timer);\n      };\n    }\n  }, [loading]);\n\n  const senderHeader = (\n    <Sender.Header\n      title=\"Attachments\"\n      styles={{\n        content: {\n          padding: 0,\n        },\n      }}\n      open={open}\n      onOpenChange={setOpen}\n      forceRender\n    >\n      <Attachments\n        ref={attachmentsRef}\n        // Mock not real upload file\n        beforeUpload={() => false}\n        items={fileList}\n        onChange={({ fileList }) => setFileList(fileList)}\n        getDropContainer={() => senderRef.current?.nativeElement}\n      />\n    </Sender.Header>\n  );\n\n  const suggestions: SuggestionItems = [\n    { label: 'Write a report', value: 'report' },\n    { label: 'Draw a picture', value: 'draw' },\n    {\n      label: 'Check some knowledge',\n      value: 'knowledge',\n      icon: <OpenAIFilled />,\n      children: [\n        {\n          label: 'About React',\n          value: 'react',\n        },\n        {\n          label: 'About Ant Design',\n          value: 'antd',\n        },\n      ],\n    },\n  ];\n\n  return (\n    <Flex vertical gap=\"middle\">\n      <Suggestion\n        items={suggestions}\n        onSelect={() => {\n          senderRef.current?.insert?.(\n            [\n              {\n                type: 'input',\n                key: `partner_2_${Date.now()}`,\n                props: { placeholder: 'Enter a name' },\n              },\n            ],\n            'cursor',\n            '@',\n          );\n        }}\n      >\n        {({ onTrigger }) => {\n          return (\n            <Sender\n              loading={loading}\n              ref={senderRef}\n              placeholder=\"Press Enter to send message\"\n              header={senderHeader}\n              footer={(actionNode) => {\n                return (\n                  <Flex justify=\"space-between\" align=\"center\">\n                    <Flex gap=\"small\" align=\"center\">\n                      <Button style={IconStyle} type=\"text\" icon={<PaperClipOutlined />} />\n                      <Switch\n                        value={deepThink}\n                        checkedChildren={\n                          <>\n                            Deep Think:<span style={SwitchTextStyle}>on</span>\n                          </>\n                        }\n                        unCheckedChildren={\n                          <>\n                            Deep Think:<span style={SwitchTextStyle}>off</span>\n                          </>\n                        }\n                        onChange={(checked: boolean) => {\n                          setDeepThink(checked);\n                        }}\n                        icon={<OpenAIOutlined />}\n                      />\n                      <Dropdown\n                        menu={{\n                          selectedKeys: [activeAgentKey],\n                          onClick: agentItemClick,\n                          items: agentItems,\n                        }}\n                      >\n                        <Switch value={false} icon={<AntDesignOutlined />}>\n                          Agent\n                        </Switch>\n                      </Dropdown>\n                      {fileItems?.length ? (\n                        <Dropdown menu={{ onClick: fileItemClick, items: fileItems }}>\n                          <Switch value={false} icon={<ProfileOutlined />}>\n                            Files\n                          </Switch>\n                        </Dropdown>\n                      ) : null}\n                    </Flex>\n                    <Flex align=\"center\">\n                      <Button type=\"text\" style={IconStyle} icon={<ApiOutlined />} />\n                      <Divider orientation=\"vertical\" />\n                      {actionNode}\n                    </Flex>\n                  </Flex>\n                );\n              }}\n              onKeyDown={(e) => {\n                if (e.key === '@') {\n                  onTrigger();\n                } else {\n                  onTrigger(false);\n                }\n              }}\n              suffix={false}\n              onSubmit={(v) => {\n                setLoading(true);\n                message.info(`Send message: ${v}`);\n                senderRef.current?.clear?.();\n              }}\n              onCancel={() => {\n                setLoading(false);\n                message.error('Cancel sending!');\n              }}\n              slotConfig={AgentInfo[activeAgentKey].slotConfig}\n              autoSize={{ minRows: 3, maxRows: 6 }}\n            />\n          );\n        }}\n      </Suggestion>\n    </Flex>\n  );\n};\n\nexport default () => <App />;\n";;
  