
  import '/Users/dupeiyi/Project/private/x/packages/x/components/sender/demo/footer.tsx?watch=parent';
  export default "import { ApiOutlined, LinkOutlined, SearchOutlined } from '@ant-design/icons';\nimport { Sender } from '@ant-design/x';\nimport { Button, Divider, Flex, Switch, theme } from 'antd';\nimport React, { useState } from 'react';\n\nconst App: React.FC = () => {\n  const { token } = theme.useToken();\n  const [loading, setLoading] = useState<boolean>(false);\n  const [value, setValue] = useState<string>('');\n\n  const iconStyle = {\n    fontSize: 18,\n    color: token.colorText,\n  };\n\n  React.useEffect(() => {\n    if (loading) {\n      const timer = setTimeout(() => {\n        setLoading(false);\n        setValue('');\n        console.log('Send message successfully!');\n      }, 2000);\n      return () => {\n        clearTimeout(timer);\n      };\n    }\n  }, [loading]);\n\n  return (\n    <Sender\n      value={value}\n      onChange={setValue}\n      autoSize={{ minRows: 2, maxRows: 6 }}\n      placeholder=\"Press Enter to send message\"\n      footer={(_, { components }) => {\n        const { SendButton, LoadingButton, SpeechButton } = components;\n        return (\n          <Flex justify=\"space-between\" align=\"center\">\n            <Flex gap=\"small\" align=\"center\">\n              <Button style={iconStyle} type=\"text\" icon={<LinkOutlined />} />\n              <Divider orientation=\"vertical\" />\n              Deep Thinking\n              <Switch size=\"small\" />\n              <Divider orientation=\"vertical\" />\n              <Button icon={<SearchOutlined />}>Global Search</Button>\n            </Flex>\n            <Flex align=\"center\">\n              <Button type=\"text\" style={iconStyle} icon={<ApiOutlined />} />\n              <Divider orientation=\"vertical\" />\n              <SpeechButton style={iconStyle} />\n              <Divider orientation=\"vertical\" />\n              {loading ? (\n                <LoadingButton type=\"default\" />\n              ) : (\n                <SendButton type=\"primary\" disabled={false} />\n              )}\n            </Flex>\n          </Flex>\n        );\n      }}\n      onSubmit={() => {\n        setLoading(true);\n      }}\n      onCancel={() => {\n        setLoading(false);\n      }}\n      suffix={false}\n    />\n  );\n};\n\nexport default App;\n";;
  