
  import '/Users/dupeiyi/Project/private/x/packages/x/docs/x-sdk/demos/x-request/timeout.tsx?watch=parent';
  export default "import { LoadingOutlined, TagsOutlined } from '@ant-design/icons';\nimport type { ThoughtChainItemType } from '@ant-design/x';\nimport { ThoughtChain } from '@ant-design/x';\nimport { XRequest } from '@ant-design/x-sdk';\nimport { Button, Descriptions, Splitter } from 'antd';\nimport React, { useState } from 'react';\n\nconst BASE_URL = 'https://api.example.com';\nconst PATH = '/chat';\n\nasync function mockFetch() {\n  return new Promise<Response>((resolve) => {\n    setTimeout(() => {\n      console.log('Response arrived');\n      resolve(\n        new Response('{ \"data\": \"Hi\" }', {\n          status: 200,\n          headers: {\n            'Content-Type': 'application/json',\n          },\n        }),\n      );\n    }, 3000);\n  });\n}\n\nconst App = () => {\n  const [status, setStatus] = useState<string>('');\n  const [thoughtChainStatus, setThoughtChainStatus] = useState<ThoughtChainItemType['status']>();\n\n  function request() {\n    setStatus('pending');\n\n    XRequest(BASE_URL + PATH, {\n      timeout: 2000,\n      callbacks: {\n        onSuccess: () => {\n          setStatus('success');\n          setThoughtChainStatus('success');\n        },\n        onError: (error) => {\n          if (error.message === 'TimeoutError') {\n            setStatus('TimeoutError');\n          }\n          setThoughtChainStatus('error');\n        },\n      },\n      fetch: mockFetch,\n    });\n  }\n\n  return (\n    <Splitter>\n      <Splitter.Panel>\n        <Button type=\"primary\" disabled={status === 'loading'} onClick={request}>\n          Request - {BASE_URL}\n          {PATH}\n        </Button>\n      </Splitter.Panel>\n      <Splitter.Panel style={{ marginLeft: 16 }}>\n        <ThoughtChain\n          items={[\n            {\n              title: 'Request Log',\n              status: thoughtChainStatus,\n              icon: status === 'pending' ? <LoadingOutlined /> : <TagsOutlined />,\n              description: `request ${status}`,\n              content: (\n                <Descriptions column={1}>\n                  <Descriptions.Item label=\"Status\">{status || '-'}</Descriptions.Item>\n                </Descriptions>\n              ),\n            },\n          ]}\n        />\n      </Splitter.Panel>\n    </Splitter>\n  );\n};\n\nexport default App;\n";;
  