
  import '/Users/dupeiyi/Project/private/x/packages/x/docs/x-sdk/demos/x-stream/default-protocol.tsx?watch=parent';
  export default "import { TagsOutlined } from '@ant-design/icons';\nimport { Bubble, ThoughtChain } from '@ant-design/x';\nimport { XStream } from '@ant-design/x-sdk';\nimport { Button, Splitter } from 'antd';\nimport React from 'react';\n\nconst contentChunks = ['He', 'llo', ', w', 'or', 'ld!'];\n\nfunction mockReadableStream() {\n  const sseChunks: string[] = [];\n\n  for (let i = 0; i < contentChunks.length; i++) {\n    const sseEventPart = `event: message\\ndata: {\"id\":\"${i}\",\"content\":\"${contentChunks[i]}\"}\\n\\n`;\n    sseChunks.push(sseEventPart);\n  }\n\n  return new ReadableStream({\n    async start(controller) {\n      for (const chunk of sseChunks) {\n        await new Promise((resolve) => setTimeout(resolve, 100));\n        controller.enqueue(new TextEncoder().encode(chunk));\n      }\n      controller.close();\n    },\n  });\n}\n\nconst App = () => {\n  const [lines, setLines] = React.useState<Record<string, string>[]>([]);\n  const content = lines.map((line) => JSON.parse(line.data).content).join('');\n\n  async function readStream() {\n    // 🌟 Read the stream\n    for await (const chunk of XStream({\n      readableStream: mockReadableStream(),\n    })) {\n      console.log(chunk);\n      setLines((pre) => [...pre, chunk]);\n    }\n  }\n\n  return (\n    <Splitter>\n      <Splitter.Panel>\n        {/* -------------- Emit -------------- */}\n        <Button type=\"primary\" onClick={readStream} style={{ marginBottom: 16 }}>\n          Mock Default Protocol - SSE\n        </Button>\n        {/* -------------- Content Concat -------------- */}\n        {content && <Bubble content={content} />}\n      </Splitter.Panel>\n      {/* -------------- Log -------------- */}\n      <Splitter.Panel style={{ marginLeft: 16 }}>\n        <ThoughtChain\n          items={\n            lines.length\n              ? [\n                  {\n                    title: 'Mock Default Protocol - Log',\n                    status: 'success',\n                    icon: <TagsOutlined />,\n                    content: (\n                      <pre style={{ overflow: 'scroll' }}>\n                        {lines.map((i) => (\n                          <code key={i.data}>{i.data}</code>\n                        ))}\n                      </pre>\n                    ),\n                  },\n                ]\n              : []\n          }\n        />\n      </Splitter.Panel>\n    </Splitter>\n  );\n};\n\nexport default App;\n";;
  