  export default "import {\n  AlipayCircleOutlined,\n  BulbOutlined,\n  CheckCircleOutlined,\n  GithubOutlined,\n  LoadingOutlined,\n  SmileOutlined,\n  UserOutlined,\n} from '@ant-design/icons';\nimport {\n  Bubble,\n  Conversations,\n  Prompts,\n  Sender,\n  Suggestion,\n  ThoughtChain,\n  XProvider,\n} from '@ant-design/x';\nimport type { ConfigProviderProps, GetProp } from 'antd';\nimport { Card, Divider, Flex, Radio, Typography } from 'antd';\nimport React from 'react';\n\nexport default () => {\n  const [value, setValue] = React.useState('');\n  const [direction, setDirection] =\n    React.useState<GetProp<ConfigProviderProps, 'direction'>>('ltr');\n\n  return (\n    <>\n      <Flex gap={12} style={{ marginBottom: 16 }} align=\"center\">\n        <Typography.Text>Direction:</Typography.Text>\n        <Radio.Group value={direction} onChange={(e) => setDirection(e.target.value)}>\n          <Radio.Button value=\"ltr\">LTR</Radio.Button>\n          <Radio.Button value=\"rtl\">RTL</Radio.Button>\n        </Radio.Group>\n      </Flex>\n\n      <Flex gap={12} vertical>\n        <Card>\n          <XProvider direction={direction}>\n            <Flex style={{ height: 500 }} gap={12}>\n              <Conversations\n                style={{ width: 200 }}\n                defaultActiveKey=\"1\"\n                items={[\n                  {\n                    key: '1',\n                    label: 'Conversation - 1',\n                    icon: <GithubOutlined />,\n                  },\n                  {\n                    key: '2',\n                    label: 'Conversation - 2',\n                    icon: <AlipayCircleOutlined />,\n                  },\n                ]}\n              />\n              <Divider orientation=\"vertical\" style={{ height: '100%' }} />\n              <Flex vertical justify=\"space-between\" style={{ flex: 1 }}>\n                <Bubble.List\n                  items={[\n                    {\n                      key: '1',\n                      role: 'user',\n                      placement: 'end',\n                      content: 'Hello Ant Design X!',\n                      avatar: <UserOutlined />,\n                    },\n                    {\n                      key: '2',\n                      role: 'ai',\n                      content: 'Hello World!',\n                    },\n                    {\n                      key: '3',\n                      role: 'ai',\n                      content: '',\n                      loading: true,\n                    },\n                  ]}\n                />\n                <Flex vertical gap={12}>\n                  <Prompts\n                    items={[\n                      {\n                        key: '1',\n                        icon: <BulbOutlined style={{ color: '#FFD700' }} />,\n                        label: 'Ignite Your Creativity',\n                      },\n                      {\n                        key: '2',\n                        icon: <SmileOutlined style={{ color: '#52C41A' }} />,\n                        label: 'Tell me a Joke',\n                      },\n                    ]}\n                  />\n                  <Suggestion items={[{ label: 'Write a report', value: 'report' }]}>\n                    {({ onTrigger, onKeyDown }) => {\n                      return (\n                        <Sender\n                          value={value}\n                          onChange={(nextVal) => {\n                            if (nextVal === '/') {\n                              onTrigger();\n                            } else if (!nextVal) {\n                              onTrigger(false);\n                            }\n                            setValue(nextVal);\n                          }}\n                          onKeyDown={onKeyDown}\n                          placeholder='Type \"/\" to trigger suggestion'\n                        />\n                      );\n                    }}\n                  </Suggestion>\n                </Flex>\n              </Flex>\n              <Divider orientation=\"vertical\" style={{ height: '100%' }} />\n              <ThoughtChain\n                style={{ width: 200 }}\n                items={[\n                  {\n                    title: 'Hello Ant Design X!',\n                    status: 'success',\n                    description: 'status: success',\n                    icon: <CheckCircleOutlined />,\n                    content: 'Ant Design X help you build AI chat/platform app as ready-to-use 📦.',\n                  },\n                  {\n                    title: 'Hello World!',\n                    status: 'success',\n                    description: 'status: success',\n                    icon: <CheckCircleOutlined />,\n                  },\n                  {\n                    title: 'Pending...',\n                    status: 'loading',\n                    description: 'status: pending',\n                    icon: <LoadingOutlined />,\n                  },\n                ]}\n              />\n            </Flex>\n          </XProvider>\n        </Card>\n      </Flex>\n    </>\n  );\n};\n";;
  