
  import '/Users/dupeiyi/Project/private/x/packages/x/docs/x-markdown/demo/codeDemo/supersets.tsx?watch=parent';
  export default "import XMarkdown, { type ComponentProps } from '@ant-design/x-markdown';\nimport HighlightCode from '@ant-design/x-markdown/plugins/HighlightCode';\nimport Latex from '@ant-design/x-markdown/plugins/Latex';\nimport Mermaid from '@ant-design/x-markdown/plugins/Mermaid';\nimport React from 'react';\nimport { useMarkdownTheme } from '../_utils';\nimport '@ant-design/x-markdown/themes/light.css';\nimport '@ant-design/x-markdown/themes/dark.css';\n\nconst content = `\n### Latex\ninline standard: $\\\\frac{df}{dt}$ \\n\nblock standard：\\n\n$$\n\\\\Delta t' = \\\\frac{\\\\Delta t}{\\\\sqrt{1 - \\\\frac{v^2}{c^2}}}\n$$\n\ninline: \\\\(\\\\frac{df}{dt}\\\\)  \\n\nblock: \\n\n\\\\[\n\\\\Delta t' = \\\\frac{\\\\Delta t}{\\\\sqrt{1 - \\\\frac{v^2}{c^2}}}\n\\\\]\n\n`;\n\nconst Code: React.FC<ComponentProps> = (props) => {\n  const { className, children } = props;\n  const lang = className?.match(/language-(\\w+)/)?.[1] || '';\n\n  if (typeof children !== 'string') return null;\n  if (lang === 'mermaid') {\n    return <Mermaid>{children}</Mermaid>;\n  }\n  return <HighlightCode lang={lang}>{children}</HighlightCode>;\n};\n\nconst App: React.FC = () => {\n  const [className] = useMarkdownTheme();\n\n  return (\n    <XMarkdown\n      className={className}\n      config={{ extensions: Latex() }}\n      components={{\n        code: Code,\n      }}\n    >\n      {content}\n    </XMarkdown>\n  );\n};\n\nexport default App;\n";;
  