
  import '/Users/dupeiyi/Project/private/x/packages/x/docs/playground/ultramodern.tsx?watch=parent';
  export default "import { DeleteOutlined, OpenAIOutlined, SyncOutlined } from '@ant-design/icons';\nimport {\n  Actions,\n  Bubble,\n  BubbleListProps,\n  Conversations,\n  Sender,\n  SenderProps,\n  XProvider,\n} from '@ant-design/x';\nimport XMarkdown from '@ant-design/x-markdown';\nimport {\n  DeepSeekChatProvider,\n  DefaultMessageInfo,\n  SSEFields,\n  useXChat,\n  useXConversations,\n  XModelMessage,\n  XModelParams,\n  XModelResponse,\n  XRequest,\n} from '@ant-design/x-sdk';\nimport { Flex, GetRef, message } from 'antd';\nimport { createStyles } from 'antd-style';\nimport classNames from 'classnames';\nimport dayjs from 'dayjs';\nimport React, { useEffect, useRef, useState } from 'react';\nimport '@ant-design/x-markdown/themes/light.css';\nimport '@ant-design/x-markdown/themes/dark.css';\nimport { useMarkdownTheme } from '../x-markdown/demo/_utils';\nimport locale from './_utils/local';\n\nconst useStyle = createStyles(({ token, css }) => {\n  return {\n    layout: css`\n      width: 100%;\n      height: 100vh;\n      display: flex;\n      background: ${token.colorBgContainer};\n      overflow: hidden;\n    `,\n    side: css`\n      background: ${token.colorBgLayout};\n      width: 280px;\n      height: 100%;\n      display: flex;\n      flex-direction: column;\n      padding: 0 12px;\n      box-sizing: border-box;\n    `,\n    logo: css`\n      display: flex;\n      align-items: center;\n      justify-content: start;\n      padding: 0 24px;\n      box-sizing: border-box;\n      gap: 8px;\n      margin: 24px 0;\n\n      span {\n        font-weight: bold;\n        color: ${token.colorText};\n        font-size: 16px;\n      }\n    `,\n    conversations: css`\n      overflow-y: auto;\n      margin-top: 12px;\n      padding: 0;\n      flex: 1;\n      .ant-conversations-list {\n        padding-inline-start: 0;\n      }\n    `,\n    sideFooter: css`\n      border-top: 1px solid ${token.colorBorderSecondary};\n      height: 40px;\n      display: flex;\n      align-items: center;\n      justify-content: space-between;\n    `,\n    chat: css`\n      height: 100%;\n      width: calc(100% - 240px);\n      overflow: auto;\n      box-sizing: border-box;\n      display: flex;\n      flex-direction: column;\n      padding-block: ${token.paddingLG}px;\n      padding-inline: ${token.paddingLG}px;\n      gap: 16px;\n      .ant-bubble-content-updating {\n        background-image: linear-gradient(90deg, #ff6b23 0%, #af3cb8 31%, #53b6ff 89%);\n        background-size: 100% 2px;\n        background-repeat: no-repeat;\n        background-position: bottom;\n      }\n    `,\n    startPage: css`\n      display: flex;\n      width: 100%;\n      max-width: 840px;\n      flex-direction: column;\n      align-items: center;\n      height: 100%;\n    `,\n    agentName: css`\n      margin-block-start: 25%;\n      font-size: 32px;\n      margin-block-end: 38px;\n      font-weight: 600;\n    `,\n    chatList: css`\n      display: flex;\n      align-items: center;\n      width: 100%;\n      height: 100%;\n      flex-direction: column;\n      justify-content: space-between;\n    `,\n  };\n});\n\n// ==================== Context ====================\nconst ChatContext = React.createContext<{\n  onReload?: ReturnType<typeof useXChat>['onReload'];\n}>({});\nconst DEFAULT_CONVERSATIONS_ITEMS = [\n  {\n    key: 'default-0',\n    label: locale.whatIsAntDesignX,\n    group: locale.today,\n  },\n  {\n    key: 'default-1',\n    label: locale.howToQuicklyInstallAndImportComponents,\n    group: locale.today,\n  },\n  {\n    key: 'default-2',\n    label: locale.newAgiHybridInterface,\n    group: locale.yesterday,\n  },\n];\nconst HISTORY_MESSAGES: {\n  [key: string]: DefaultMessageInfo<XModelMessage>[];\n} = {\n  'default-1': [\n    {\n      message: { role: 'user', content: locale.howToQuicklyInstallAndImportComponents },\n      status: 'success',\n    },\n    {\n      message: {\n        role: 'assistant',\n        content: locale.aiMessage_2,\n      },\n      status: 'success',\n    },\n  ],\n  'default-2': [\n    { message: { role: 'user', content: locale.newAgiHybridInterface }, status: 'success' },\n    {\n      message: {\n        role: 'assistant',\n        content: locale.aiMessage_1,\n      },\n      status: 'success',\n    },\n  ],\n};\n\nconst slotConfig: SenderProps['slotConfig'] = [\n  { type: 'text', value: locale.slotTextStart },\n  {\n    type: 'select',\n    key: 'destination',\n    props: {\n      defaultValue: 'X SDK',\n      options: ['X SDK', 'X Markdown', 'Bubble'],\n    },\n  },\n  { type: 'text', value: locale.slotTextEnd },\n];\nconst historyMessageFactory = (conversationKey: string): DefaultMessageInfo<XModelMessage>[] => {\n  return HISTORY_MESSAGES[conversationKey] || [];\n};\nconst providerCaches = new Map<string, DeepSeekChatProvider>();\nconst providerFactory = (conversationKey: string) => {\n  if (!providerCaches.get(conversationKey)) {\n    providerCaches.set(\n      conversationKey,\n      new DeepSeekChatProvider({\n        request: XRequest<XModelParams, Partial<Record<SSEFields, XModelResponse>>>(\n          'https://api.x.ant.design/api/big_model_glm-4.5-flash',\n          {\n            manual: true,\n            params: {\n              stream: true,\n              model: 'glm-4.5-flash',\n            },\n          },\n        ),\n      }),\n    );\n  }\n  return providerCaches.get(conversationKey);\n};\nconst Footer: React.FC<{\n  id?: string;\n  content: string;\n  status?: string;\n}> = ({ id, content, status }) => {\n  const context = React.useContext(ChatContext);\n  const Items = [\n    {\n      key: 'retry',\n      label: locale.retry,\n      icon: <SyncOutlined />,\n      onItemClick: () => {\n        if (id) {\n          context?.onReload?.(id, {\n            userAction: 'retry',\n          });\n        }\n      },\n    },\n    {\n      key: 'copy',\n      actionRender: <Actions.Copy text={content} />,\n    },\n  ];\n  return status !== 'updating' && status !== 'loading' ? (\n    <div style={{ display: 'flex' }}>{id && <Actions items={Items} />}</div>\n  ) : null;\n};\n\nconst getRole = (className: string): BubbleListProps['role'] => ({\n  assistant: {\n    placement: 'start',\n    footer: (content, { status, key }) => (\n      <Footer content={content} status={status} id={key as string} />\n    ),\n    contentRender: (content: any, { status }) => {\n      const newContent = content.replace('/\\n\\n/g', '<br/><br/>');\n      return (\n        <XMarkdown\n          paragraphTag=\"div\"\n          className={className}\n          streaming={{\n            hasNextChunk: status === 'updating',\n            enableAnimation: true,\n          }}\n        >\n          {newContent}\n        </XMarkdown>\n      );\n    },\n  },\n  user: { placement: 'end' },\n});\n\nconst App = () => {\n  const [className] = useMarkdownTheme();\n  const senderRef = useRef<GetRef<typeof Sender>>(null);\n  const { conversations, addConversation, setConversations } = useXConversations({\n    defaultConversations: DEFAULT_CONVERSATIONS_ITEMS,\n  });\n  const [curConversation, setCurConversation] = useState<string>(\n    DEFAULT_CONVERSATIONS_ITEMS[0].key,\n  );\n\n  const [activeConversation, setActiveConversation] = useState<string>();\n\n  // ==================== Runtime ====================\n\n  const { onRequest, messages, isRequesting, abort, onReload } = useXChat({\n    provider: providerFactory(curConversation), // every conversation has its own provider\n    conversationKey: curConversation,\n    defaultMessages: historyMessageFactory(curConversation),\n    requestPlaceholder: () => {\n      return {\n        content: locale.noData,\n        role: 'assistant',\n      };\n    },\n    requestFallback: (_, { messageInfo }) => {\n      return {\n        ...messageInfo?.message,\n        content: messageInfo?.message.content || locale.requestFailedPleaseTryAgain,\n      };\n    },\n  });\n\n  const { styles } = useStyle();\n  const [messageApi, contextHolder] = message.useMessage();\n  const [deepThink, setDeepThink] = useState<boolean>(true);\n\n  useEffect(() => {\n    senderRef.current!.focus({\n      cursor: 'end',\n    });\n  }, [senderRef.current]);\n  return (\n    <XProvider locale={locale}>\n      {contextHolder}\n      <ChatContext.Provider value={{ onReload }}>\n        <div className={styles.layout}>\n          <div className={styles.side}>\n            <div className={styles.logo}>\n              <img\n                src=\"https://mdn.alipayobjects.com/huamei_iwk9zp/afts/img/A*eco6RrQhxbMAAAAAAAAAAAAADgCCAQ/original\"\n                draggable={false}\n                alt=\"logo\"\n                width={24}\n                height={24}\n              />\n              <span>Ant Design X</span>\n            </div>\n            <Conversations\n              creation={{\n                onClick: () => {\n                  if (messages.length === 0) {\n                    messageApi.error(locale.itIsNowANewConversation);\n                    return;\n                  }\n                  const now = dayjs().valueOf().toString();\n                  addConversation({\n                    key: now,\n                    label: `${locale.newConversation} ${conversations.length + 1}`,\n                    group: locale.today,\n                  });\n                  setCurConversation(now);\n                },\n              }}\n              items={conversations\n                .map(({ key, label, ...other }) => ({\n                  key,\n                  label: key === activeConversation ? `[${locale.curConversation}]${label}` : label,\n                  ...other,\n                }))\n                .sort(({ key }) => (key === activeConversation ? -1 : 0))}\n              className={styles.conversations}\n              activeKey={curConversation}\n              onActiveChange={async (val) => {\n                setCurConversation(val);\n              }}\n              groupable\n              styles={{ item: { padding: '0 8px' } }}\n              menu={(conversation) => ({\n                items: [\n                  {\n                    label: locale.delete,\n                    key: 'delete',\n                    icon: <DeleteOutlined />,\n                    danger: true,\n                    onClick: () => {\n                      const newList = conversations.filter((item) => item.key !== conversation.key);\n                      const newKey = newList?.[0]?.key;\n                      setConversations(newList);\n                      if (conversation.key === curConversation) {\n                        setCurConversation(newKey);\n                      }\n                    },\n                  },\n                ],\n              })}\n            />\n          </div>\n          <div className={styles.chat}>\n            <div className={styles.chatList}>\n              {messages?.length !== 0 && (\n                /* 🌟 消息列表 */\n                <Bubble.List\n                  style={{\n                    height: 'calc(100% - 160px)',\n                  }}\n                  items={messages?.map((i) => ({\n                    ...i.message,\n                    key: i.id,\n                    status: i.status,\n                    loading: i.status === 'loading',\n                    extraInfo: i.message.extraInfo,\n                  }))}\n                  styles={{\n                    root: {\n                      marginBlockEnd: 24,\n                    },\n                    bubble: { maxWidth: 840 },\n                  }}\n                  role={getRole(className)}\n                />\n              )}\n              <div\n                style={{ width: '100%', maxWidth: 840 }}\n                className={classNames({ [styles.startPage]: messages.length === 0 })}\n              >\n                {messages.length === 0 && (\n                  <div className={styles.agentName}>{locale.agentName}</div>\n                )}\n                <Sender\n                  suffix={false}\n                  ref={senderRef}\n                  key={curConversation}\n                  slotConfig={slotConfig}\n                  loading={isRequesting}\n                  onSubmit={(val) => {\n                    if (!val) return;\n                    onRequest({\n                      messages: [{ role: 'user', content: val }],\n                      thinking: {\n                        type: 'disabled',\n                      },\n                    });\n                    setActiveConversation(curConversation);\n                    senderRef.current?.clear?.();\n                  }}\n                  onCancel={() => {\n                    abort();\n                  }}\n                  placeholder={locale.placeholder}\n                  footer={(actionNode) => {\n                    return (\n                      <Flex justify=\"space-between\" align=\"center\">\n                        <Flex gap=\"small\" align=\"center\">\n                          <Sender.Switch\n                            value={deepThink}\n                            onChange={(checked: boolean) => {\n                              setDeepThink(checked);\n                            }}\n                            icon={<OpenAIOutlined />}\n                          >\n                            {locale.deepThink}\n                          </Sender.Switch>\n                        </Flex>\n                        <Flex align=\"center\">{actionNode}</Flex>\n                      </Flex>\n                    );\n                  }}\n                  autoSize={{ minRows: 3, maxRows: 6 }}\n                />\n              </div>\n            </div>\n          </div>\n        </div>\n      </ChatContext.Provider>\n    </XProvider>\n  );\n};\n\nexport default App;\n";;
  