
  import '/Users/dupeiyi/Project/private/x/packages/x/components/bubble/demo/custom-content.tsx?watch=parent';
  export default "import { Bubble } from '@ant-design/x';\nimport { Button, Flex, Image } from 'antd';\nimport React, { useState } from 'react';\n\ntype ContentType = {\n  imageUrl: string;\n  text: string;\n  actionNode: React.ReactNode;\n};\n\nconst App = () => {\n  const [content, setContent] = useState<ContentType>({\n    imageUrl:\n      'https://mdn.alipayobjects.com/huamei_iwk9zp/afts/img/A*eco6RrQhxbMAAAAAAAAAAAAADgCCAQ/original',\n    text: 'Ant Design X',\n    actionNode: <>Click Me</>,\n  });\n\n  return (\n    <div style={{ height: 100 }}>\n      <Bubble<ContentType>\n        content={content}\n        contentRender={(content) => {\n          return (\n            <Flex gap=\"middle\" align=\"center\">\n              <Image height={50} src={content.imageUrl} />\n              <span style={{ fontSize: 18, fontWeight: 'bold' }}>{content.text}</span>\n            </Flex>\n          );\n        }}\n        footer={(content) => {\n          return (\n            <Button\n              onClick={() => {\n                setContent((ori) => ({\n                  ...ori,\n                  actionNode: <>🎉 Happy Ant Design X !</>,\n                }));\n              }}\n              type=\"text\"\n            >\n              {content?.actionNode}\n            </Button>\n          );\n        }}\n      />\n    </div>\n  );\n};\n\nexport default App;\n";;
  