  export default "import { CopyOutlined, UserOutlined } from '@ant-design/icons';\nimport { Bubble } from '@ant-design/x';\nimport { Avatar, Flex, Tooltip } from 'antd';\nimport React from 'react';\n\nconst App = () => (\n  <Flex vertical gap=\"small\">\n    <Flex gap=\"small\" wrap>\n      <div style={{ width: '100%' }}>\n        <Bubble\n          content=\"align left\"\n          avatar={\n            <Tooltip title=\"main side\">\n              <Avatar icon={<UserOutlined />} />\n            </Tooltip>\n          }\n          extra={\n            <Tooltip title=\"extra side\">\n              <CopyOutlined />\n            </Tooltip>\n          }\n        />\n      </div>\n    </Flex>\n    <Flex gap=\"small\" wrap>\n      <div style={{ width: '100%' }}>\n        <Bubble\n          content=\"align right\"\n          placement=\"end\"\n          avatar={\n            <Tooltip title=\"main side\">\n              <Avatar icon={<UserOutlined />} />\n            </Tooltip>\n          }\n          extra={\n            <Tooltip title=\"extra side\">\n              <CopyOutlined />\n            </Tooltip>\n          }\n        />\n      </div>\n    </Flex>\n  </Flex>\n);\n\nexport default App;\n";;
  