
  import '/Users/dupeiyi/Project/private/x/packages/x/components/actions/demo/preset.tsx?watch=parent';
  export default "import { CheckOutlined, ShareAltOutlined } from '@ant-design/icons';\nimport type { ActionsFeedbackProps, ActionsItemProps } from '@ant-design/x';\nimport { Actions } from '@ant-design/x';\nimport { message, Pagination } from 'antd';\nimport React, { useState } from 'react';\n\nconst App: React.FC = () => {\n  // pagination\n  const [curPage, setCurPage] = useState(1);\n  // feedback\n  const [feedbackStatus, setFeedbackStatus] = useState<ActionsFeedbackProps['value']>('default');\n\n  // audio\n  const [audioStatus, setAudioStatus] = useState<ActionsItemProps['status']>('default');\n  // share\n  const [shareStatus, setShareStatus] = useState<ActionsItemProps['status']>('default');\n\n  const onClick = (type: 'share' | 'audio') => {\n    let timer: NodeJS.Timeout | null = null;\n    const dispatchFN = type === 'share' ? setShareStatus : setAudioStatus;\n    const status = type === 'share' ? shareStatus : audioStatus;\n    switch (status) {\n      case 'default':\n        dispatchFN('loading');\n        timer = setTimeout(() => {\n          timer && clearTimeout(timer);\n          dispatchFN('running');\n        }, 1500);\n        break;\n      case 'running':\n        dispatchFN('loading');\n        timer = setTimeout(() => {\n          timer && clearTimeout(timer);\n          dispatchFN('default');\n        }, 1500);\n        break;\n    }\n  };\n\n  const items = [\n    {\n      key: 'pagination',\n      actionRender: () => (\n        <Pagination\n          simple\n          current={curPage}\n          onChange={(page) => setCurPage(page)}\n          total={5}\n          pageSize={1}\n        />\n      ),\n    },\n    {\n      key: 'feedback',\n      actionRender: () => (\n        <Actions.Feedback\n          value={feedbackStatus}\n          styles={{\n            liked: {\n              color: '#f759ab',\n            },\n          }}\n          onChange={(val) => {\n            setFeedbackStatus(val);\n            message.success(`Change feedback value to: ${val}`);\n          }}\n          key=\"feedback\"\n        />\n      ),\n    },\n    {\n      key: 'copy',\n      label: 'copy',\n      actionRender: () => {\n        return <Actions.Copy text=\"copy value\" />;\n      },\n    },\n    {\n      key: 'audio',\n      label: 'audio',\n      actionRender: () => {\n        return <Actions.Audio onClick={() => onClick('audio')} status={audioStatus} />;\n      },\n    },\n    {\n      key: 'share',\n      label: 'share',\n      actionRender: () => {\n        return (\n          <Actions.Item\n            onClick={() => onClick('share')}\n            label={shareStatus}\n            status={shareStatus}\n            defaultIcon={<ShareAltOutlined />}\n            runningIcon={<CheckOutlined />}\n          />\n        );\n      },\n    },\n  ];\n\n  return <Actions items={items} />;\n};\n\nexport default App;\n";;
  