
  import '/Users/dupeiyi/Project/private/x/packages/x/docs/playground/agent-tbox.tsx?watch=parent';
  export default "import {\n  AppstoreAddOutlined,\n  DeleteOutlined,\n  EditOutlined,\n  EllipsisOutlined,\n  FileSearchOutlined,\n  GlobalOutlined,\n  HeartOutlined,\n  ProductOutlined,\n  QuestionCircleOutlined,\n  ScheduleOutlined,\n  ShareAltOutlined,\n  SmileOutlined,\n  SyncOutlined,\n} from '@ant-design/icons';\nimport type { ActionsFeedbackProps, BubbleListProps, ThoughtChainItemProps } from '@ant-design/x';\nimport {\n  Actions,\n  Bubble,\n  Conversations,\n  Prompts,\n  Sender,\n  Think,\n  ThoughtChain,\n  Welcome,\n  XProvider,\n} from '@ant-design/x';\nimport enUS_X from '@ant-design/x/locale/en_US';\nimport zhCN_X from '@ant-design/x/locale/zh_CN';\nimport XMarkdown, { type ComponentProps } from '@ant-design/x-markdown';\nimport type { TransformMessage } from '@ant-design/x-sdk';\nimport {\n  AbstractChatProvider,\n  AbstractXRequestClass,\n  useXChat,\n  useXConversations,\n  XRequestOptions,\n} from '@ant-design/x-sdk';\nimport { Avatar, Button, Flex, type GetProp, message, Pagination, Space } from 'antd';\nimport enUS_antd from 'antd/locale/en_US';\nimport zhCN_antd from 'antd/locale/zh_CN';\nimport { createStyles } from 'antd-style';\nimport dayjs from 'dayjs';\nimport React, { useState } from 'react';\nimport { TboxClient } from 'tbox-nodejs-sdk';\nimport { useMarkdownTheme } from '../x-markdown/demo/_utils';\n\n// ==================== Local ====================\nconst zhCN = {\n  whatIsTbox: '什么是百宝箱 Tbox.cn?',\n  whatCanTboxDo: '百宝箱可以做什么?',\n  today: '今天',\n  yesterday: '昨天',\n  hotTopics: '最热话题',\n  designGuide: '设计指南',\n  intent: '意图',\n  role: '角色',\n  aiUnderstandsUserNeeds: 'AI 理解用户需求并提供解决方案',\n  aiPublicImage: 'AI 的公众形象',\n  dynamic: '动态',\n  component: '组件',\n  guide: '指南',\n  tutorial: '教程',\n  newConversation: '新会话',\n  rename: '重命名',\n  delete: '删除',\n  requestInProgress: '请求正在进行中，请等待请求完成。',\n  demoButtonNoFunction: '演示按钮，无实际功能',\n  helloAntdXTboxAgent: '你好， 我是 Ant Design X & 百宝箱智能体',\n  antdXTboxDescription:\n    '基于 Ant Design 的 AGI 产品界面解决方案，打造更卓越的智能视觉体验，集成了百宝箱 Tbox.cn 的智能体能力，助力产品设计与开发。',\n  askMeAnything: '向我提问吧',\n  DeepThinking: '深度思考中',\n  CompleteThinking: '深度思考完成',\n  noData: '暂无数据',\n  modelIsRunning: '正在调用模型',\n  modelExecutionCompleted: '大模型执行完成',\n  executionFailed: '执行失败',\n  aborted: '已经终止',\n  curConversation: '当前对话',\n  nowNenConversation: '当前已经是新会话',\n  isMock: '当前为模拟功能',\n  retry: '重新生成',\n};\n\nconst enUS = {\n  whatIsTbox: 'What is Tbox.cn?',\n  whatCanTboxDo: 'What can Tbox.cn do?',\n  today: 'Today',\n  yesterday: 'Yesterday',\n  hotTopics: 'Hot Topics',\n  designGuide: 'Design Guide',\n  intent: 'Intent',\n  role: 'Role',\n  aiUnderstandsUserNeeds: 'AI understands user needs and provides solutions',\n  aiPublicImage: \"AI's public image\",\n  dynamic: 'Dynamic',\n  component: 'Component',\n  guide: 'Guide',\n  tutorial: 'Tutorial',\n  newConversation: 'New Conversation',\n  rename: 'Rename',\n  delete: 'Delete',\n  requestInProgress: 'Request is in progress, please wait for the request to complete.',\n  demoButtonNoFunction: 'Demo button, no actual function',\n  helloAntdXTboxAgent: 'Hello, I am Ant Design X & Tbox Agent',\n  antdXTboxDescription:\n    'An AGI product interface solution based on Ant Design, creating a superior intelligent visual experience, integrating the capabilities of Tbox.cn agents to assist in product design and development.',\n  askMeAnything: 'Ask me anything...',\n  DeepThinking: 'Deep thinking',\n  CompleteThinking: 'Deep thinking completed',\n  noData: 'No Data',\n  modelIsRunning: 'Model is running',\n  modelExecutionCompleted: 'Model execution completed',\n  executionFailed: 'Execution failed',\n  aborted: 'Aborted',\n  curConversation: 'Current Conversation',\n  nowNenConversation: 'It is now a new conversation.',\n  retry: 'retry',\n  isMock: 'It is Mock',\n};\n\nconst isZhCN = window.parent?.location?.pathname?.includes('-cn');\nconst t = isZhCN ? zhCN : enUS;\n\n// ==================== Static Config ====================\nconst DEFAULT_CONVERSATIONS_ITEMS = [\n  {\n    key: 'default-0',\n    label: t.whatIsTbox,\n    group: t.today,\n  },\n  {\n    key: 'default-1',\n    label: t.whatCanTboxDo,\n    group: t.yesterday,\n  },\n];\n\nconst HOT_TOPICS = {\n  key: '1',\n  label: t.hotTopics,\n  children: [\n    {\n      key: '1-1',\n      description: t.whatIsTbox,\n      icon: <span style={{ color: '#f93a4a', fontWeight: 700 }}>1</span>,\n    },\n    {\n      key: '1-2',\n      description: t.whatCanTboxDo,\n      icon: <span style={{ color: '#ff6565', fontWeight: 700 }}>2</span>,\n    },\n  ],\n};\n\nconst DESIGN_GUIDE = {\n  key: '2',\n  label: t.designGuide,\n  children: [\n    {\n      key: '2-1',\n      icon: <HeartOutlined />,\n      label: t.intent,\n      description: t.aiUnderstandsUserNeeds,\n    },\n    {\n      key: '2-2',\n      icon: <SmileOutlined />,\n      label: t.role,\n      description: t.aiPublicImage,\n    },\n  ],\n};\n\nconst SENDER_PROMPTS: GetProp<typeof Prompts, 'items'> = [\n  {\n    key: '1',\n    description: t.dynamic,\n    icon: <ScheduleOutlined />,\n  },\n  {\n    key: '2',\n    description: t.component,\n    icon: <ProductOutlined />,\n  },\n  {\n    key: '3',\n    description: t.guide,\n    icon: <FileSearchOutlined />,\n  },\n  {\n    key: '4',\n    description: t.tutorial,\n    icon: <AppstoreAddOutlined />,\n  },\n];\n\n// ==================== Style ====================\nconst useStyle = createStyles(({ token, css }) => {\n  return {\n    layout: css`\n      width: 100%;\n      height: 100vh;\n      display: flex;\n      background: ${token.colorBgContainer};\n      font-family: AlibabaPuHuiTi, ${token.fontFamily}, sans-serif;\n    `,\n    // sider 样式\n    sider: css`\n      background: ${token.colorBgLayout}80;\n      width: 280px;\n      height: 100%;\n      display: flex;\n      flex-direction: column;\n      padding: 0 12px;\n      box-sizing: border-box;\n    `,\n    logo: css`\n      display: flex;\n      align-items: center;\n      justify-content: start;\n      padding: 0 24px;\n      box-sizing: border-box;\n      gap: 8px;\n      margin: 24px 0;\n\n      span {\n        font-weight: bold;\n        color: ${token.colorText};\n        font-size: 16px;\n      }\n    `,\n    conversations: css`\n      flex: 1;\n      overflow-y: auto;\n      margin-top: 12px;\n      padding: 0;\n\n      .ant-conversations-list {\n        padding-inline-start: 0;\n      }\n    `,\n    sideFooter: css`\n      border-top: 1px solid ${token.colorBorderSecondary};\n      height: 40px;\n      display: flex;\n      align-items: center;\n      justify-content: space-between;\n    `,\n    typing: css`\n      position: absolute;\n      right: 20px;\n      bottom: 10px;\n    `,\n    // chat list 样式\n    chat: css`\n      height: 100%;\n      width: calc(100% - 280px);\n      box-sizing: border-box;\n      display: flex;\n      flex-direction: column;\n      padding-block: ${token.paddingLG}px;\n      justify-content: space-between;\n      .ant-bubble-content-updating {\n        background-image: linear-gradient(90deg, #ff6b23 0%, #af3cb8 31%, #53b6ff 89%);\n        background-size: 100% 2px;\n        background-repeat: no-repeat;\n        background-position: bottom;\n      }\n    `,\n    chatPrompt: css`\n      .ant-prompts-label {\n        color: #000000e0 !important;\n      }\n      .ant-prompts-desc {\n        color: #000000a6 !important;\n        width: 100%;\n      }\n      .ant-prompts-icon {\n        color: #000000a6 !important;\n      }\n    `,\n    chatList: css`\n      display: flex;\n      height: calc(100% - 120px);\n      flex-direction: column;\n      align-items: center;\n      width: 100%;\n    `,\n    placeholder: css`\n      padding-top: 32px;\n      width: 100%;\n      padding-inline: ${token.paddingLG}px;\n      box-sizing: border-box;\n    `,\n    // sender 样式\n    sender: css`\n      width: 100%;\n      max-width: 840px;\n      margin: 0 auto;\n    `,\n    speechButton: css`\n      font-size: 18px;\n      color: ${token.colorText} !important;\n    `,\n    senderPrompt: css`\n      width: 100%;\n      max-width: 840px;\n      margin: 0 auto;\n      color: ${token.colorText};\n    `,\n  };\n});\n\n// ==================== TboxProvider ====================\n\ninterface TboxInput {\n  message: {\n    role: string;\n    content: string;\n  };\n  userAction?: string;\n}\n\ninterface TboxOutput {\n  text?: string;\n  ext_text?: string;\n}\ninterface TboxMessage {\n  content: TboxOutput;\n  role: string;\n}\n\nconst tboxClient = new TboxClient({\n  httpClientConfig: {\n    authorization: 'your-api-key', // Replace with your API key\n    isAntdXDemo: true, // Only for Ant Design X demo\n  },\n});\nclass TboxRequest<\n  Input extends TboxInput = TboxInput,\n  Output extends TboxOutput = TboxOutput,\n> extends AbstractXRequestClass<Input, Output> {\n  tboxClient: TboxClient;\n  tboxStream: any;\n\n  _isTimeout = false;\n  _isStreamTimeout = false;\n  _isRequesting = false;\n\n  constructor(baseURL: string, options: XRequestOptions<Input, Output>) {\n    super(baseURL, options);\n    this.tboxClient = new TboxClient({\n      httpClientConfig: {\n        authorization: 'your-api-key', // Replace with your API key\n        isAntdXDemo: true, // Only for Ant Design X demo\n      },\n    });\n  }\n  get asyncHandler(): Promise<any> {\n    return Promise.resolve();\n  }\n  get isTimeout(): boolean {\n    return this._isTimeout;\n  }\n  get isStreamTimeout(): boolean {\n    return this._isStreamTimeout;\n  }\n  get isRequesting(): boolean {\n    return this._isRequesting;\n  }\n  get manual(): boolean {\n    return true;\n  }\n  run(params?: Input | undefined): void {\n    const stream = tboxClient.chat({\n      appId: 'your-app-id', // Replace with your app ID\n      query: params?.message?.content,\n      version: 'v2', // only for antd-x v2\n      userId: 'antd-x',\n    } as any);\n    this.tboxStream = stream;\n    const { callbacks } = this.options;\n\n    const dataArr: Output[] = [];\n\n    stream.on('data', (data) => {\n      let parsedPayload: any;\n      try {\n        const payload = (data as any).data?.payload || '{}';\n        parsedPayload = JSON.parse(payload);\n      } catch (e) {\n        console.error('Failed to parse payload:', e);\n        return;\n      }\n\n      if (parsedPayload?.text || parsedPayload?.ext_data?.text) {\n        const data = {\n          text: parsedPayload?.text,\n          ext_text: parsedPayload?.ext_data?.text,\n        } as Output;\n        dataArr.push(data);\n        callbacks?.onUpdate?.(data, new Headers());\n      }\n    });\n\n    stream.on('error', (error) => {\n      if (!error?.message?.includes('abort')) {\n        callbacks?.onError(error);\n      }\n    });\n\n    stream.on('end', () => {\n      callbacks?.onSuccess(dataArr, new Headers());\n    });\n\n    stream.on('abort', () => {\n      callbacks?.onError({ name: 'AbortError', message: '' });\n    });\n  }\n  abort(): void {\n    this.tboxStream?.abort?.();\n  }\n}\n\nclass TboxProvider<\n  ChatMessage extends TboxMessage = TboxMessage,\n  Input extends TboxInput = TboxInput,\n  Output extends TboxOutput = TboxOutput,\n> extends AbstractChatProvider<ChatMessage, Input, Output> {\n  transformParams(requestParams: Partial<Input>, options: XRequestOptions<Input, Output>): Input {\n    if (typeof requestParams !== 'object') {\n      throw new Error('requestParams must be an object');\n    }\n    if (requestParams.userAction === 'retry') {\n      const messages = this.getMessages();\n      const queryMessage = (messages || [])?.reverse().find(({ role }) => {\n        return role === 'user';\n      });\n      return {\n        message: queryMessage,\n        ...(options?.params || {}),\n        ...(requestParams || {}),\n      } as Input;\n    }\n\n    return {\n      ...(options?.params || {}),\n      ...(requestParams || {}),\n    } as Input;\n  }\n  transformLocalMessage(requestParams: Partial<Input>): ChatMessage {\n    return requestParams.message as unknown as ChatMessage;\n  }\n  transformMessage(info: TransformMessage<ChatMessage, Output>): ChatMessage {\n    const { originMessage, chunk } = info || {};\n    if (!chunk) {\n      return {\n        content: originMessage?.content || {},\n        role: 'assistant',\n      } as ChatMessage;\n    }\n\n    const content = originMessage?.content || {};\n    return {\n      content: {\n        text: (content.text || '') + (chunk.text || ''),\n        ext_text: (content.ext_text || '') + (chunk.ext_text || ''),\n      },\n      role: 'assistant',\n    } as ChatMessage;\n  }\n}\n\n/**\n * 🔔 Please replace the BASE_URL, MODEL with your own values.\n */\nconst providerCaches = new Map<string, TboxProvider>();\nconst providerFactory = (conversationKey: string) => {\n  if (!providerCaches.get(conversationKey)) {\n    providerCaches.set(\n      conversationKey,\n      new TboxProvider({\n        request: new TboxRequest('Tbox Client', {}),\n      }),\n    );\n  }\n  return providerCaches.get(conversationKey);\n};\n\n// ==================== Context ====================\nconst ChatContext = React.createContext<{\n  onReload?: ReturnType<typeof useXChat>['onReload'];\n}>({});\n\n// ==================== Sub Component====================\nconst ThinkComponent = React.memo((props: ComponentProps) => {\n  const [title, setTitle] = React.useState(`${t.DeepThinking}...`);\n  const [loading, setLoading] = React.useState(true);\n\n  React.useEffect(() => {\n    if (props.streamStatus === 'done') {\n      setTitle(t.CompleteThinking);\n      setLoading(false);\n    }\n  }, [props.streamStatus]);\n\n  return (\n    <Think title={title} loading={loading}>\n      {props.children}\n    </Think>\n  );\n});\n\nconst Footer: React.FC<{\n  id?: number | string;\n  content: string;\n  status?: string;\n}> = ({ id, content, status }) => {\n  const context = React.useContext(ChatContext);\n  const [mockFeedback, setMockFeedback] = useState<ActionsFeedbackProps['value']>('default');\n  const Items = [\n    {\n      key: 'pagination',\n      actionRender: <Pagination simple total={1} pageSize={1} />,\n    },\n    {\n      key: 'retry',\n      label: t.retry,\n      icon: <SyncOutlined />,\n      onItemClick: () => {\n        if (id) {\n          context?.onReload?.(id, {\n            userAction: 'retry',\n          });\n        }\n      },\n    },\n    {\n      key: 'copy',\n      actionRender: <Actions.Copy text={content} />,\n    },\n    {\n      key: 'audio',\n      actionRender: (\n        <Actions.Audio\n          onClick={() => {\n            message.info(t.isMock);\n          }}\n        />\n      ),\n    },\n    {\n      key: 'feedback',\n      actionRender: (\n        <Actions.Feedback\n          styles={{\n            liked: {\n              color: '#f759ab',\n            },\n          }}\n          value={mockFeedback || 'default'}\n          key=\"feedback\"\n          onChange={(val) => {\n            setMockFeedback(val);\n            message.success(`${id}: ${val}`);\n          }}\n        />\n      ),\n    },\n  ];\n\n  return status !== 'updating' && status !== 'loading' ? (\n    <div style={{ display: 'flex' }}>{id && <Actions items={Items} />}</div>\n  ) : null;\n};\n\nconst AgentTbox: React.FC = () => {\n  const { styles } = useStyle();\n  const [className] = useMarkdownTheme();\n  const locale = isZhCN ? { ...zhCN_antd, ...zhCN_X } : { ...enUS_antd, ...enUS_X };\n  // ==================== State ====================\n\n  const {\n    conversations,\n    activeConversationKey,\n    setActiveConversationKey,\n    addConversation,\n    setConversations,\n  } = useXConversations({\n    defaultConversations: DEFAULT_CONVERSATIONS_ITEMS,\n    defaultActiveConversationKey: DEFAULT_CONVERSATIONS_ITEMS[0].key,\n  });\n\n  const [messageApi, contextHolder] = message.useMessage();\n\n  const [inputValue, setInputValue] = useState('');\n\n  /**\n   * 🔔 Please replace the BASE_URL, PATH, MODEL, API_KEY with your own values.\n   */\n\n  // ==================== Runtime ====================\n\n  const { onRequest, messages, isRequesting, abort, onReload } = useXChat({\n    provider: providerFactory(activeConversationKey), // every conversation has its own provider\n    conversationKey: activeConversationKey,\n    requestPlaceholder: () => {\n      return {\n        content: { text: t.noData },\n        role: 'assistant',\n      };\n    },\n  });\n\n  // ==================== Event ====================\n  const onSubmit = (val: string) => {\n    if (!val) return;\n\n    onRequest({\n      message: { role: 'user', content: val },\n    });\n  };\n\n  // ==================== Nodes ====================\n  const chatSide = (\n    <div className={styles.sider}>\n      {/* 🌟 Logo */}\n      <div className={styles.logo}>\n        <img\n          src=\"https://mdn.alipayobjects.com/huamei_iwk9zp/afts/img/A*eco6RrQhxbMAAAAAAAAAAAAADgCCAQ/original\"\n          draggable={false}\n          alt=\"logo\"\n          width={24}\n          height={24}\n        />\n        <span>Ant Design X</span>\n      </div>\n      {/* 🌟 会话管理 */}\n      <Conversations\n        creation={{\n          onClick: () => {\n            if (messages.length === 0) {\n              messageApi.error(t.nowNenConversation);\n              return;\n            }\n            const now = dayjs().valueOf().toString();\n            addConversation({\n              key: now,\n              label: `${t.newConversation} ${conversations.length + 1}`,\n              group: t.today,\n            });\n            setActiveConversationKey(now);\n          },\n        }}\n        items={conversations.map(({ key, label }) => ({\n          key,\n          label: key === activeConversationKey ? `[${t.curConversation}]${label}` : label,\n        }))}\n        className={styles.conversations}\n        activeKey={activeConversationKey}\n        onActiveChange={setActiveConversationKey}\n        groupable\n        styles={{ item: { padding: '0 8px' } }}\n        menu={(conversation) => ({\n          items: [\n            {\n              label: t.rename,\n              key: 'rename',\n              icon: <EditOutlined />,\n            },\n            {\n              label: t.delete,\n              key: 'delete',\n              icon: <DeleteOutlined />,\n              danger: true,\n              onClick: () => {\n                const newList = conversations.filter((item) => item.key !== conversation.key);\n                const newKey = newList?.[0]?.key;\n                setConversations(newList);\n                if (conversation.key === activeConversationKey) {\n                  setActiveConversationKey(newKey);\n                }\n              },\n            },\n          ],\n        })}\n      />\n\n      <div className={styles.sideFooter}>\n        <Avatar size={24} />\n        <Button type=\"text\" icon={<QuestionCircleOutlined />} />\n      </div>\n    </div>\n  );\n  const ThoughtChainConfig = {\n    loading: {\n      title: t.modelIsRunning,\n      status: 'loading',\n    },\n    updating: {\n      title: t.modelIsRunning,\n      status: 'loading',\n    },\n    success: {\n      title: t.modelExecutionCompleted,\n      status: 'success',\n    },\n    error: {\n      title: t.executionFailed,\n      status: 'error',\n    },\n    abort: {\n      title: t.aborted,\n      status: 'abort',\n    },\n  };\n\n  const role: BubbleListProps['role'] = {\n    assistant: {\n      placement: 'start',\n      header: (_, { status }) => {\n        const config = ThoughtChainConfig[status as keyof typeof ThoughtChainConfig];\n        return config ? (\n          <ThoughtChain.Item\n            style={{\n              marginBottom: 8,\n            }}\n            status={config.status as ThoughtChainItemProps['status']}\n            variant=\"solid\"\n            icon={<GlobalOutlined />}\n            title={config.title}\n          />\n        ) : null;\n      },\n      footer: (content, { status, key }) => <Footer content={content} status={status} id={key} />,\n      contentRender: (content, { status }) => {\n        const markdownText = `${content.ext_text ? `<think>\\n\\n${content.ext_text}${content.text ? '\\n\\n</think>\\n\\n' : ''}` : ''}${content.text || ''}`;\n        return (\n          <XMarkdown\n            content={markdownText as string}\n            className={className}\n            components={{\n              think: ThinkComponent,\n            }}\n            streaming={{ hasNextChunk: status === 'updating', enableAnimation: true }}\n          />\n        );\n      },\n    },\n    user: { placement: 'end' },\n  };\n  const chatList = (\n    <div className={styles.chatList}>\n      {messages?.length ? (\n        /* 🌟 消息列表 */\n        <Bubble.List\n          items={messages?.map((i) => ({\n            ...i.message,\n            status: i.status,\n            loading: i.status === 'loading',\n            key: i.id,\n          }))}\n          styles={{\n            bubble: {\n              maxWidth: 840,\n            },\n          }}\n          role={role}\n        />\n      ) : (\n        <Flex\n          vertical\n          style={{\n            maxWidth: 840,\n          }}\n          gap={16}\n          align=\"center\"\n          className={styles.placeholder}\n        >\n          <Welcome\n            variant=\"borderless\"\n            icon=\"https://mdn.alipayobjects.com/huamei_iwk9zp/afts/img/A*s5sNRo5LjfQAAAAAAAAAAAAADgCCAQ/fmt.webp\"\n            title={t.helloAntdXTboxAgent}\n            description={t.antdXTboxDescription}\n            extra={\n              <Space>\n                <Button icon={<ShareAltOutlined />} />\n                <Button icon={<EllipsisOutlined />} />\n              </Space>\n            }\n          />\n          <Flex gap={16}>\n            <Prompts\n              items={[HOT_TOPICS]}\n              styles={{\n                list: { height: '100%' },\n                item: {\n                  flex: 1,\n                  backgroundImage: 'linear-gradient(123deg, #e5f4ff 0%, #efe7ff 100%)',\n                  borderRadius: 12,\n                  border: 'none',\n                },\n                subItem: { padding: 0, background: 'transparent' },\n              }}\n              onItemClick={(info) => {\n                onSubmit(info.data.description as string);\n              }}\n              className={styles.chatPrompt}\n            />\n\n            <Prompts\n              items={[DESIGN_GUIDE]}\n              styles={{\n                item: {\n                  flex: 1,\n                  backgroundImage: 'linear-gradient(123deg, #e5f4ff 0%, #efe7ff 100%)',\n                  borderRadius: 12,\n                  border: 'none',\n                },\n                subItem: { background: '#ffffffa6' },\n              }}\n              onItemClick={(info) => {\n                onSubmit(info.data.description as string);\n              }}\n              className={styles.chatPrompt}\n            />\n          </Flex>\n        </Flex>\n      )}\n    </div>\n  );\n  const chatSender = (\n    <Flex\n      vertical\n      gap={12}\n      justify=\"center\"\n      style={{\n        marginInline: 24,\n      }}\n    >\n      {/* 🌟 提示词 */}\n      <Prompts\n        items={SENDER_PROMPTS}\n        onItemClick={(info) => {\n          onSubmit(info.data.description as string);\n        }}\n        styles={{\n          item: { padding: '6px 12px' },\n        }}\n        className={styles.senderPrompt}\n      />\n      {/* 🌟 输入框 */}\n      <Sender\n        value={inputValue}\n        onSubmit={() => {\n          onSubmit(inputValue);\n          setInputValue('');\n        }}\n        onChange={setInputValue}\n        onCancel={() => {\n          abort();\n        }}\n        loading={isRequesting}\n        className={styles.sender}\n        placeholder={t.askMeAnything}\n      />\n    </Flex>\n  );\n\n  // ==================== Render =================\n  return (\n    <XProvider locale={locale}>\n      <ChatContext.Provider value={{ onReload }}>\n        {contextHolder}\n        <div className={styles.layout}>\n          {chatSide}\n          <div className={styles.chat}>\n            {chatList}\n            {chatSender}\n          </div>\n        </div>\n      </ChatContext.Provider>\n    </XProvider>\n  );\n};\n\nexport default AgentTbox;\n";;
  