
  import '/Users/dupeiyi/Project/private/x/packages/x/components/think/demo/status.tsx?watch=parent';
  export default "import { OpenAIOutlined, SyncOutlined } from '@ant-design/icons';\nimport { Think } from '@ant-design/x';\nimport { css, Global } from '@emotion/react';\nimport { Button } from 'antd';\nimport React from 'react';\n\nconst App = () => {\n  const [title, setTitle] = React.useState('Complete thinking');\n  const [loading, setLoading] = React.useState(false);\n\n  const handleClick = () => {\n    setLoading(true);\n    setTitle('deep thinking');\n\n    setTimeout(() => {\n      setLoading(false);\n      setTitle('Complete thinking');\n    }, 2000);\n  };\n  return (\n    <>\n      <div>\n        <Button onClick={handleClick}>Run</Button>\n      </div>\n      <br />\n      <Think title={title} blink loading={loading}>\n        This is deep thinking content.\n      </Think>\n      <br />\n      <Global\n        styles={css`\n        :root {\n          @keyframes spin {\n            from {\n                transform: rotate(0deg);\n            }\n            to {\n                transform: rotate(360deg);\n            }\n          }\n        }\n        `}\n      />\n      <Think\n        title={title}\n        loading={\n          loading ? (\n            <SyncOutlined style={{ fontSize: 12, animation: 'spin 1s linear infinite' }} />\n          ) : (\n            false\n          )\n        }\n        icon={<OpenAIOutlined />}\n      >\n        Customize status icon.\n      </Think>\n    </>\n  );\n};\n\nexport default App;\n";;
  