  export default "import { Actions, Bubble, BubbleItemType } from '@ant-design/x';\nimport type { GetProp, GetRef } from 'antd';\nimport { Flex, Spin } from 'antd';\nimport React, { useState } from 'react';\n\nconst roles = (\n  setMessage: React.Dispatch<React.SetStateAction<BubbleItemType[]>>,\n): GetProp<typeof Bubble.List, 'role'> => ({\n  ai: {\n    placement: 'start',\n    typing: (_, { status }) =>\n      status === 'updating' ? { effect: 'typing', step: 5, interval: 20 } : false,\n    footer: (content, { key, extraInfo }) => (\n      <Actions\n        items={[\n          {\n            key: 'copy',\n            label: 'copy',\n            actionRender: () => {\n              return <Actions.Copy text={content} />;\n            },\n          },\n          {\n            key: 'feedback',\n            actionRender: () => (\n              <Actions.Feedback\n                value={extraInfo?.feedback || 'default'}\n                styles={{\n                  liked: {\n                    color: '#f759ab',\n                  },\n                }}\n                onChange={(val) => {\n                  setMessage((messages) =>\n                    messages.map((message) => {\n                      if (message.key === key) {\n                        message.extraInfo = { feedback: val };\n                      }\n                      return message;\n                    }),\n                  );\n                }}\n                key=\"feedback\"\n              />\n            ),\n          },\n        ]}\n      />\n    ),\n    loadingRender: () => (\n      <Flex align=\"center\" gap=\"small\">\n        <Spin size=\"small\" />\n        Custom loading...\n      </Flex>\n    ),\n  },\n  user: {\n    placement: 'end',\n  },\n});\n\nconst App = () => {\n  const listRef = React.useRef<GetRef<typeof Bubble.List>>(null);\n  const [message, setMessage] = useState<BubbleItemType[]>([\n    {\n      status: 'success',\n      key: 'welcome',\n      role: 'ai',\n      variant: 'borderless',\n      content: 'Mock welcome content. '.repeat(10),\n      extraInfo: {\n        feedback: 'like',\n      },\n    },\n    {\n      key: 'ask',\n      role: 'user',\n      content: 'Mock user content.',\n    },\n    {\n      key: 'ai_0',\n      role: 'ai',\n      status: 'success',\n      variant: 'borderless',\n      content: 'Mock welcome content. '.repeat(10),\n      extraInfo: {\n        feedback: 'dislike',\n      },\n    },\n    {\n      key: 'user_1',\n      role: 'user',\n      content: 'Mock user content.',\n    },\n    {\n      key: 'ai_1',\n      role: 'ai',\n      variant: 'borderless',\n      status: 'success',\n      content: 'Mock welcome content. '.repeat(10),\n      extraInfo: {\n        feedback: 'dislike',\n      },\n    },\n    {\n      key: 'user_2',\n      role: 'user',\n      content: 'Mock user content.',\n    },\n    {\n      key: 'ai_2',\n      role: 'ai',\n      variant: 'borderless',\n      status: 'success',\n      content: 'Mock welcome content. '.repeat(10),\n      extraInfo: {\n        feedback: 'like',\n      },\n    },\n    {\n      key: 'user_3',\n      role: 'user',\n      content: 'Mock user content.',\n    },\n    {\n      key: 'ai_3',\n      role: 'ai',\n      status: 'loading',\n      loading: true,\n      content: '',\n    },\n  ]);\n  return (\n    <Bubble.List ref={listRef} style={{ height: 500 }} role={roles(setMessage)} items={message} />\n  );\n};\n\nexport default App;\n";;
  