
  import '/Users/dupeiyi/Project/private/x/packages/x/components/actions/demo/fadeIn.tsx?watch=parent';
  export default "import { RedoOutlined } from '@ant-design/icons';\nimport type { ActionsProps } from '@ant-design/x';\nimport { Actions } from '@ant-design/x';\nimport { Button, Flex, message, Pagination, Switch } from 'antd';\nimport React, { useState } from 'react';\n\nconst App: React.FC = () => {\n  // pagination\n  const [curPage, setCurPage] = useState(1);\n  const [key, setKey] = useState(0);\n  const [fadeInLeft, setFadeInLeft] = useState(true);\n\n  const actionItems = [\n    {\n      key: 'pagination',\n      actionRender: () => (\n        <Pagination\n          simple\n          current={curPage}\n          onChange={(page) => setCurPage(page)}\n          total={5}\n          pageSize={1}\n        />\n      ),\n    },\n    {\n      key: 'retry',\n      icon: <RedoOutlined />,\n      label: 'Retry',\n    },\n    {\n      key: 'copy',\n      label: 'copy',\n      actionRender: () => {\n        return <Actions.Copy text=\"copy value\" />;\n      },\n    },\n  ];\n  const onClick: ActionsProps['onClick'] = ({ keyPath }) => {\n    // Logic for handling click events\n    message.success(`you clicked ${keyPath.join(',')}`);\n  };\n  return (\n    <Flex gap=\"middle\" vertical>\n      <Flex gap=\"middle\" align=\"center\">\n        <Switch\n          style={{ alignSelf: 'flex-end' }}\n          checkedChildren=\"fadeInLeft\"\n          unCheckedChildren=\"fadeIn\"\n          value={fadeInLeft}\n          onChange={setFadeInLeft}\n        />\n        <Button style={{ alignSelf: 'flex-end' }} onClick={() => setKey(key + 1)}>\n          Re-Render\n        </Button>\n      </Flex>\n      <Actions\n        key={key}\n        fadeIn={!fadeInLeft}\n        fadeInLeft={fadeInLeft}\n        items={actionItems}\n        onClick={onClick}\n        variant=\"borderless\"\n      />\n    </Flex>\n  );\n};\n\nexport default App;\n";;
  