
  import '/Users/dupeiyi/Project/private/x/packages/x/components/suggestion/demo/basic.tsx?watch=parent';
  export default "import { OpenAIFilled } from '@ant-design/icons';\nimport { Sender, Suggestion } from '@ant-design/x';\nimport type { GetProp } from 'antd';\nimport React from 'react';\n\ntype SuggestionItems = Exclude<GetProp<typeof Suggestion, 'items'>, () => void>;\n\nconst suggestions: SuggestionItems = [\n  { label: 'Write a report', value: 'report' },\n  { label: 'Draw a picture', value: 'draw' },\n  {\n    label: 'Check some knowledge',\n    value: 'knowledge',\n    icon: <OpenAIFilled />,\n    children: [\n      {\n        label: 'About React',\n        value: 'react',\n      },\n      {\n        label: 'About Ant Design',\n        value: 'antd',\n      },\n    ],\n  },\n];\n\nconst Demo: React.FC = () => {\n  const [value, setValue] = React.useState('');\n\n  return (\n    <Suggestion\n      items={suggestions}\n      onSelect={(itemVal) => {\n        setValue(`[${itemVal}]:`);\n      }}\n    >\n      {({ onTrigger, onKeyDown }) => {\n        return (\n          <Sender\n            value={value}\n            onChange={(nextVal) => {\n              if (nextVal === '/') {\n                onTrigger();\n              } else if (!nextVal) {\n                onTrigger(false);\n              }\n              setValue(nextVal);\n            }}\n            onKeyDown={onKeyDown}\n            placeholder=\"输入 / 获取建议\"\n          />\n        );\n      }}\n    </Suggestion>\n  );\n};\n\nexport default Demo;\n";;
  