
  import '/Users/dupeiyi/Project/private/x/packages/x/docs/x-markdown/demo/codeDemo/walkTokens.tsx?watch=parent';
  export default "import type { Token } from '@ant-design/x-markdown';\nimport { XMarkdown } from '@ant-design/x-markdown';\nimport React from 'react';\nimport { useIntl } from 'react-intl';\nimport { useMarkdownTheme } from '../_utils';\nimport '@ant-design/x-markdown/themes/light.css';\nimport '@ant-design/x-markdown/themes/dark.css';\n\nconst ZH_Markdown = `请查看：[这是一个链接](https://xxxxx)`;\n\nconst EN_Markdown = `Please check: [This is a link](https://xxxxx)`;\n\nconst App: React.FC = () => {\n  const [className] = useMarkdownTheme();\n  const { locale } = useIntl();\n  const content = locale === 'zh-CN' ? ZH_Markdown : EN_Markdown;\n  const message = locale === 'zh-CN' ? '当前链接不合法' : 'Invalid link';\n\n  const walkTokens = (token: Token) => {\n    if (token.type === 'link') {\n      // 请求接口判断是否合法\n      // await fetch(token.href);\n      delete token.tokens;\n      token.raw = message;\n      token.text = message;\n      token.type = 'text';\n    }\n  };\n\n  return (\n    <XMarkdown className={className} config={{ walkTokens }}>\n      {content}\n    </XMarkdown>\n  );\n};\n\nexport default App;\n";;
  