
  import '/Users/dupeiyi/Project/private/x/packages/x/components/conversations/demo/infinite-load.tsx?watch=parent';
  export default "import { RedoOutlined } from '@ant-design/icons';\nimport { Conversations, type ConversationsProps } from '@ant-design/x';\nimport type { GetProp } from 'antd';\nimport { Avatar, Divider, Spin, theme } from 'antd';\nimport React, { useEffect, useState } from 'react';\nimport InfiniteScroll from 'react-infinite-scroll-component';\n\nconst App: React.FC = () => {\n  const [loading, setLoading] = useState(false);\n  const [data, setData] = useState<GetProp<ConversationsProps, 'items'>>([]);\n\n  const { token } = theme.useToken();\n\n  // Customize the style of the container\n  const style = {\n    width: 280,\n    height: 600,\n    background: token.colorBgContainer,\n    borderRadius: token.borderRadius,\n    overflow: 'auto',\n  };\n\n  const loadMoreData = () => {\n    if (loading) {\n      return;\n    }\n    setLoading(true);\n    fetch('https://randomuser.me/api/?results=10&inc=name,gender,email,nat,picture&noinfo')\n      .then((res) => res.json())\n      .then((body) => {\n        const formmatedData = body.results.map((i: any) => ({\n          key: i.email,\n          label: `${i.name.title} ${i.name.first} ${i.name.last}`,\n          icon: <Avatar src={i.picture.thumbnail} />,\n          group: i.nat,\n        }));\n\n        setData([...data, ...formmatedData]);\n        setLoading(false);\n      })\n      .catch(() => {\n        setLoading(false);\n      });\n  };\n\n  useEffect(() => {\n    loadMoreData();\n  }, []);\n\n  return (\n    <div id=\"scrollableDiv\" style={style}>\n      <InfiniteScroll\n        dataLength={data.length}\n        next={loadMoreData}\n        hasMore={data.length < 50}\n        loader={\n          <div style={{ textAlign: 'center' }}>\n            <Spin indicator={<RedoOutlined spin />} size=\"small\" />\n          </div>\n        }\n        endMessage={<Divider plain>It is all, nothing more 🤐</Divider>}\n        scrollableTarget=\"scrollableDiv\"\n        style={{ overflow: 'hidden' }}\n      >\n        <Conversations items={data} defaultActiveKey=\"demo1\" groupable />\n      </InfiniteScroll>\n    </div>\n  );\n};\n\nexport default App;\n";;
  