
  import '/Users/dupeiyi/Project/private/x/packages/x/components/file-card/demo/mask.tsx?watch=parent';
  export default "import { VerticalAlignBottomOutlined } from '@ant-design/icons';\nimport { FileCard } from '@ant-design/x';\nimport React from 'react';\n\nconst App = () => {\n  const onDownload = () => {\n    const blob = new Blob(['123'], { type: 'text/plain' });\n    const a = document.createElement('a');\n    const href = URL.createObjectURL(blob);\n    a.href = href;\n    a.download = 'txt-file.txt';\n    document.body.appendChild(a);\n    a.click();\n    document.body.removeChild(a);\n    URL.revokeObjectURL(href);\n  };\n\n  return (\n    <FileCard\n      name=\"txt-file.txt\"\n      byte={1024}\n      mask={<VerticalAlignBottomOutlined style={{ fontSize: 20 }} onClick={() => onDownload()} />}\n    />\n  );\n};\n\nexport default App;\n";;
  