  export default "import {\n  BulbOutlined,\n  InfoCircleOutlined,\n  RocketOutlined,\n  SmileOutlined,\n  WarningOutlined,\n} from '@ant-design/icons';\nimport type { PromptsProps } from '@ant-design/x';\nimport { Prompts } from '@ant-design/x';\nimport { Button, Flex, message, Switch } from 'antd';\nimport React, { useState } from 'react';\n\nconst items: PromptsProps['items'] = [\n  {\n    key: '1',\n    icon: <BulbOutlined style={{ color: '#FFD700' }} />,\n    label: 'Ignite Your Creativity',\n    description: 'Got any sparks for a new project?',\n  },\n  {\n    key: '2',\n    icon: <InfoCircleOutlined style={{ color: '#1890FF' }} />,\n    label: 'Uncover Background Info',\n    description: 'Help me understand the background of this topic.',\n  },\n  {\n    key: '3',\n    icon: <RocketOutlined style={{ color: '#722ED1' }} />,\n    label: 'Efficiency Boost Battle',\n    description: 'How can I work faster and better?',\n  },\n  {\n    key: '4',\n    icon: <SmileOutlined style={{ color: '#52C41A' }} />,\n    label: 'Tell me a Joke',\n    description: 'Why do not ants get sick? Because they have tiny ant-bodies!',\n  },\n  {\n    key: '5',\n    icon: <WarningOutlined style={{ color: '#FF4D4F' }} />,\n    label: 'Common Issue Solutions',\n    description: 'How to solve common issues? Share some tips!',\n  },\n];\n\nconst App: React.FC = () => {\n  const [key, setKey] = useState(0);\n  const [fadeInLeft, setFadeInLeft] = useState(true);\n\n  return (\n    <Flex gap=\"middle\" vertical>\n      <Flex gap=\"middle\" align=\"center\">\n        <Switch\n          style={{ alignSelf: 'flex-end' }}\n          checkedChildren=\"fadeInLeft\"\n          unCheckedChildren=\"fadeIn\"\n          value={fadeInLeft}\n          onChange={setFadeInLeft}\n        />\n        <Button style={{ alignSelf: 'flex-end' }} onClick={() => setKey(key + 1)}>\n          Re-Render\n        </Button>\n      </Flex>\n      <Prompts\n        key={key}\n        fadeIn={!fadeInLeft}\n        fadeInLeft={fadeInLeft}\n        title=\"✨ Inspirational Sparks and Marvelous Tips\"\n        items={items}\n        onItemClick={(info) => {\n          message.success(`You clicked a prompt: ${info.data.label}`);\n        }}\n      />\n    </Flex>\n  );\n};\n\nexport default App;\n";;
  