  export default "import { OpenAIOutlined } from '@ant-design/icons';\nimport { Sender } from '@ant-design/x';\nimport { App, Space, Spin, Typography } from 'antd';\nimport React from 'react';\n\nconst Demo: React.FC = () => {\n  const [loading, setLoading] = React.useState(false);\n  const [value, setValue] = React.useState<string>('');\n\n  const { message } = App.useApp();\n\n  React.useEffect(() => {\n    if (loading) {\n      const timer = setTimeout(() => {\n        setLoading(false);\n        setValue('');\n        message.success('Send message successfully!');\n      }, 2000);\n      return () => {\n        clearTimeout(timer);\n      };\n    }\n  }, [loading]);\n\n  return (\n    <Sender\n      submitType=\"shiftEnter\"\n      value={value}\n      loading={loading}\n      onChange={setValue}\n      onSubmit={() => {\n        setLoading(true);\n      }}\n      onCancel={() => {\n        setLoading(false);\n      }}\n      suffix={(_, info) => {\n        const { SendButton, LoadingButton, ClearButton, SpeechButton } = info.components;\n        return (\n          <Space size=\"small\">\n            <Typography.Text style={{ whiteSpace: 'nowrap' }} type=\"secondary\">\n              <small>`Shift + Enter` to submit</small>\n            </Typography.Text>\n            <ClearButton />\n            <SpeechButton />\n            {loading ? (\n              <LoadingButton\n                type=\"default\"\n                icon={\n                  <Spin\n                    styles={{\n                      indicator: {\n                        color: '#fff',\n                      },\n                    }}\n                    size=\"small\"\n                  />\n                }\n                disabled\n              />\n            ) : (\n              <SendButton type=\"primary\" icon={<OpenAIOutlined />} disabled={false} />\n            )}\n          </Space>\n        );\n      }}\n    />\n  );\n};\n\nexport default () => (\n  <App>\n    <Demo />\n  </App>\n);\n";;
  