
  import '/Users/dupeiyi/Project/private/x/packages/x/components/sender/demo/ref-action.tsx?watch=parent';
  export default "import { Sender } from '@ant-design/x';\nimport { Button, Flex, type GetRef } from 'antd';\nimport React, { useRef } from 'react';\n\nconst App: React.FC = () => {\n  const senderRef = useRef<GetRef<typeof Sender>>(null);\n\n  const senderProps = {\n    defaultValue: 'Hello, welcome to use Ant Design X!',\n    ref: senderRef,\n  };\n\n  return (\n    <Flex wrap gap={12}>\n      <Button\n        onClick={() => {\n          senderRef.current!.insert('some text');\n        }}\n      >\n        Insert Text\n      </Button>\n      <Button\n        onClick={() => {\n          senderRef.current!.insert('some text', 'end');\n        }}\n      >\n        Insert Text End\n      </Button>\n      <Button\n        onClick={() => {\n          senderRef.current!.insert('some text', 'start');\n        }}\n      >\n        Insert Text Start\n      </Button>\n      <Button\n        onClick={() => {\n          senderRef.current!.focus({\n            cursor: 'start',\n          });\n        }}\n      >\n        Focus at first\n      </Button>\n      <Button\n        onClick={() => {\n          senderRef.current!.focus({\n            cursor: 'end',\n          });\n        }}\n      >\n        Focus at last\n      </Button>\n      <Button\n        onClick={() => {\n          senderRef.current!.focus({\n            cursor: 'all',\n          });\n        }}\n      >\n        Focus to select all\n      </Button>\n      <Button\n        onClick={() => {\n          senderRef.current!.focus({\n            preventScroll: true,\n          });\n        }}\n      >\n        Focus prevent scroll\n      </Button>\n      <Button\n        onClick={() => {\n          senderRef.current!.blur();\n        }}\n      >\n        Blur\n      </Button>\n      <Sender {...senderProps} />\n    </Flex>\n  );\n};\n\nexport default App;\n";;
  