  export default "import { SearchOutlined } from '@ant-design/icons';\nimport { Sender } from '@ant-design/x';\nimport { Flex } from 'antd';\nimport React from 'react';\nimport SemanticPreview from '../../../.dumi/components/SemanticPreview';\nimport useLocale from '../../../.dumi/hooks/useLocale';\n\nconst locales = {\n  cn: {\n    root: '根节点',\n    icon: '图标',\n    title: '标题',\n    content: '内容',\n  },\n  en: {\n    root: 'Root',\n    icon: 'Icon',\n    title: 'Title',\n    content: 'Content',\n  },\n};\n\nconst App: React.FC = () => {\n  const [locale] = useLocale(locales);\n\n  return (\n    <Flex vertical>\n      {/* Basic */}\n      <SemanticPreview\n        componentName=\"Sender.Switch\"\n        semantics={[\n          { name: 'root', desc: locale.root },\n          { name: 'icon', desc: locale.icon },\n          { name: 'title', desc: locale.title },\n          { name: 'content', desc: locale.content },\n        ]}\n      >\n        <Sender.Switch\n          checkedChildren={'Deep Search: on'}\n          unCheckedChildren={'Deep Search: off'}\n          icon={<SearchOutlined />}\n        />\n      </SemanticPreview>\n    </Flex>\n  );\n};\n\nexport default App;\n";;
  