
  import '/Users/dupeiyi/Project/private/x/packages/x/components/bubble/demo/markdown.tsx?watch=parent';
  export default "import type { BubbleProps } from '@ant-design/x';\nimport { Bubble } from '@ant-design/x';\nimport XMarkdown from '@ant-design/x-markdown';\nimport { Button, Flex, Typography } from 'antd';\n/* eslint-disable react/no-danger */\nimport React from 'react';\n\nconst text = `\n> Render as markdown content to show rich text!\n\nLink: [Ant Design X](https://x.ant.design)\n`.trim();\n\nconst renderMarkdown: BubbleProps['contentRender'] = (content) => {\n  return (\n    <Typography>\n      <XMarkdown content={content} />\n    </Typography>\n  );\n};\n\nconst App = () => {\n  const [index, setIndex] = React.useState(text.length);\n\n  React.useEffect(() => {\n    if (index < text.length) {\n      const timerId = setTimeout(() => {\n        setIndex((prevIndex) => prevIndex + 5);\n      }, 20);\n      return () => clearTimeout(timerId);\n    }\n  }, [index, text.length]);\n\n  return (\n    <Flex vertical style={{ height: 150 }} gap={16}>\n      <Flex>\n        <Button type=\"primary\" onClick={() => setIndex(1)}>\n          rerender\n        </Button>\n      </Flex>\n      <Flex>\n        <Bubble content={text.slice(0, index)} contentRender={renderMarkdown} />\n      </Flex>\n    </Flex>\n  );\n};\n\nexport default App;\n";;
  