  export default "import { SearchOutlined } from '@ant-design/icons';\nimport { Sender } from '@ant-design/x';\nimport { Flex } from 'antd';\nimport React from 'react';\n\nconst App: React.FC = () => {\n  const [value, setValue] = React.useState<boolean>(false);\n\n  return (\n    <Flex vertical gap=\"middle\">\n      <Flex align=\"center\" gap=\"small\">\n        Default: <Sender.Switch icon={<SearchOutlined />}>Deep Search</Sender.Switch>\n      </Flex>\n      <Flex align=\"center\" gap=\"small\">\n        Custom checked/unchecked content:\n        <Sender.Switch\n          checkedChildren={'Deep Search: on'}\n          unCheckedChildren={'Deep Search: off'}\n          icon={<SearchOutlined />}\n        />\n      </Flex>\n      <Flex align=\"center\" gap=\"small\">\n        Disabled:\n        <Sender.Switch disabled icon={<SearchOutlined />}>\n          Deep Search\n        </Sender.Switch>\n      </Flex>\n      <Flex align=\"center\" gap=\"small\">\n        Loading:\n        <Sender.Switch loading icon={<SearchOutlined />}>\n          Deep Search\n        </Sender.Switch>\n      </Flex>\n      <Flex align=\"center\" gap=\"small\">\n        DefaultValue:\n        <Sender.Switch\n          icon={<SearchOutlined />}\n          defaultValue={true}\n          onChange={(checked) => {\n            console.log('Switch toggled', checked);\n          }}\n        >\n          Deep Search\n        </Sender.Switch>\n      </Flex>\n      <Flex align=\"center\" gap=\"small\">\n        Controlled mode:\n        <Sender.Switch icon={<SearchOutlined />} value={value} onChange={setValue}>\n          Deep Search\n        </Sender.Switch>\n      </Flex>\n    </Flex>\n  );\n};\n\nexport default () => <App />;\n";;
  