
  import '/Users/dupeiyi/Project/private/x/packages/x/docs/react/demo/qwen-sdk.tsx?watch=parent';
  export default "import { SyncOutlined } from '@ant-design/icons';\nimport type { BubbleListProps } from '@ant-design/x';\nimport { Bubble, Sender } from '@ant-design/x';\nimport XMarkdown from '@ant-design/x-markdown';\nimport {\n  OpenAIChatProvider,\n  useXChat,\n  XModelParams,\n  XModelResponse,\n  XRequest,\n} from '@ant-design/x-sdk';\nimport { Button, Flex, Tooltip } from 'antd';\nimport React from 'react';\n\n/**\n * 🔔 Please replace the BASE_URL, PATH, MODEL, API_KEY with your own values.\n */\n\nconst BASE_URL = 'https://api.x.ant.design/api/llm_cloudflare_qwq-32b';\n\n/**\n * 🔔 The MODEL is fixed in the current request, please replace it with your BASE_UR and MODEL\n */\n\nconst MODEL = 'qwq-32b';\n\nconst role: BubbleListProps['role'] = {\n  assistant: {\n    placement: 'start',\n\n    contentRender(content: string) {\n      // Double '\\n' in a mark will causes markdown parse as a new paragraph, so we need to replace it with a single '\\n'\n      const newContent = content.replace('/\\n\\n/g', '<br/><br/>');\n      return <XMarkdown content={newContent} />;\n    },\n  },\n  user: {\n    placement: 'end',\n  },\n};\n\nconst App = () => {\n  const [content, setContent] = React.useState('');\n  const [provider] = React.useState(\n    new OpenAIChatProvider({\n      request: XRequest<XModelParams, XModelResponse>(BASE_URL, {\n        manual: true,\n        params: {\n          model: MODEL,\n          stream: true,\n        },\n      }),\n    }),\n  );\n  // Chat messages\n  const { onRequest, messages, isRequesting, abort, onReload } = useXChat({\n    provider,\n    requestFallback: (_, { error }) => {\n      if (error.name === 'AbortError') {\n        return {\n          content: 'Request is aborted',\n          role: 'assistant',\n        };\n      }\n      return {\n        content: 'Request failed, please try again!',\n        role: 'assistant',\n      };\n    },\n    requestPlaceholder: () => {\n      return {\n        content: 'Please wait...',\n        role: 'assistant',\n      };\n    },\n  });\n  return (\n    <Flex vertical gap=\"middle\">\n      <Bubble.List\n        role={role}\n        style={{ maxHeight: 300 }}\n        items={messages.map(({ id, message, status }) => ({\n          key: id,\n          role: message.role,\n          status: status,\n          loading: status === 'loading',\n          content: message.content,\n          components:\n            message.role === 'assistant'\n              ? {\n                  footer: (\n                    <Tooltip title=\"Retry\">\n                      <Button\n                        size=\"small\"\n                        type=\"text\"\n                        icon={<SyncOutlined />}\n                        style={{ marginInlineEnd: 'auto' }}\n                        onClick={() =>\n                          onReload(id, {\n                            userAction: 'retry',\n                          })\n                        }\n                      />\n                    </Tooltip>\n                  ),\n                }\n              : {},\n        }))}\n      />\n      <Sender\n        loading={isRequesting}\n        value={content}\n        onCancel={() => {\n          abort();\n        }}\n        onChange={setContent}\n        onSubmit={(nextContent) => {\n          onRequest({\n            messages: [\n              {\n                role: 'user',\n                content: nextContent,\n              },\n            ],\n          });\n          setContent('');\n        }}\n      />\n    </Flex>\n  );\n};\n\nexport default App;\n";;
  