  export default "import { Sources } from '@ant-design/x';\nimport { Button } from 'antd';\nimport React, { useState } from 'react';\n\nconst App = () => {\n  const items = [\n    {\n      title: '1. Data source',\n      url: 'https://x.ant.design/components/overview',\n    },\n    {\n      title: '2. Data source',\n      url: 'https://x.ant.design/components/overview',\n    },\n    {\n      title: '3. Data source',\n      url: 'https://x.ant.design/components/overview',\n    },\n  ];\n  const [value, setValue] = useState(true);\n  return (\n    <>\n      <Button onClick={() => setValue(!value)} style={{ marginBottom: '8px' }}>\n        Change expand\n      </Button>\n      <br />\n      <Sources\n        title={'Used 3 sources'}\n        items={items}\n        expanded={value}\n        onExpand={(value) => {\n          setValue(value);\n        }}\n      />\n    </>\n  );\n};\n\nexport default App;\n";;
  