
  import '/Users/dupeiyi/Project/private/x/packages/x/components/conversations/demo/new-chat.tsx?watch=parent';
  export default "import {\n  CodeOutlined,\n  FileImageOutlined,\n  FileSearchOutlined,\n  SignatureOutlined,\n} from '@ant-design/icons';\nimport type { ConversationsProps } from '@ant-design/x';\nimport { Conversations } from '@ant-design/x';\nimport type { GetProp } from 'antd';\nimport { theme } from 'antd';\nimport React, { useState } from 'react';\n\nconst agentItems: GetProp<ConversationsProps, 'items'> = [\n  {\n    key: 'write',\n    label: 'Help Me Write',\n    icon: <SignatureOutlined />,\n  },\n  {\n    key: 'coding',\n    label: 'AI Coding',\n    icon: <CodeOutlined />,\n  },\n  {\n    key: 'createImage',\n    label: 'Create Image',\n    icon: <FileImageOutlined />,\n  },\n  {\n    key: 'deepSearch',\n    label: 'Deep Search',\n    icon: <FileSearchOutlined />,\n  },\n  {\n    type: 'divider',\n  },\n];\n\nconst App: React.FC = () => {\n  const { token } = theme.useToken();\n  // Customize the style of the container\n  const style = {\n    width: 256,\n    background: token.colorBgContainer,\n    borderRadius: token.borderRadius,\n  };\n\n  const [historicalItems, setHistoricalItems] = useState<GetProp<ConversationsProps, 'items'>>([\n    {\n      key: `item1`,\n      label: 'Conversation Item 1',\n      group: 'Today',\n    },\n  ]);\n\n  const items = [...agentItems, ...historicalItems];\n\n  const newChatClick = () => {\n    setHistoricalItems((ori) => {\n      return [\n        ...ori,\n        {\n          key: `item${ori.length + 1}`,\n          label: `Conversation Item ${ori.length + 1}`,\n          group: 'Today',\n        },\n      ];\n    });\n  };\n\n  return (\n    <Conversations\n      creation={{\n        onClick: newChatClick,\n      }}\n      items={items}\n      defaultActiveKey=\"write\"\n      style={style}\n      groupable\n    />\n  );\n};\n\nexport default App;\n";;
  