
  import '/Users/dupeiyi/Project/private/x/packages/x/docs/playground/independent.tsx?watch=parent';
  export default "import {\n  AppstoreAddOutlined,\n  CloudUploadOutlined,\n  CommentOutlined,\n  DeleteOutlined,\n  EditOutlined,\n  EllipsisOutlined,\n  FileSearchOutlined,\n  GlobalOutlined,\n  HeartOutlined,\n  PaperClipOutlined,\n  ProductOutlined,\n  QuestionCircleOutlined,\n  ScheduleOutlined,\n  ShareAltOutlined,\n  SmileOutlined,\n  SyncOutlined,\n} from '@ant-design/icons';\nimport type { ActionsFeedbackProps, BubbleListProps, ThoughtChainItemProps } from '@ant-design/x';\nimport {\n  Actions,\n  Attachments,\n  Bubble,\n  Conversations,\n  Prompts,\n  Sender,\n  Think,\n  ThoughtChain,\n  Welcome,\n  XProvider,\n} from '@ant-design/x';\nimport type { ComponentProps } from '@ant-design/x-markdown';\nimport XMarkdown from '@ant-design/x-markdown';\nimport type { DefaultMessageInfo } from '@ant-design/x-sdk';\nimport {\n  DeepSeekChatProvider,\n  SSEFields,\n  useXChat,\n  useXConversations,\n  XModelMessage,\n  XModelParams,\n  XModelResponse,\n  XRequest,\n} from '@ant-design/x-sdk';\nimport { Avatar, Button, Flex, type GetProp, message, Pagination, Space } from 'antd';\nimport { createStyles } from 'antd-style';\nimport dayjs from 'dayjs';\nimport React, { useState } from 'react';\nimport '@ant-design/x-markdown/themes/light.css';\nimport '@ant-design/x-markdown/themes/dark.css';\nimport { useMarkdownTheme } from '../x-markdown/demo/_utils';\nimport locale from './_utils/local';\n\n// ==================== Style ====================\nconst useStyle = createStyles(({ token, css }) => {\n  return {\n    layout: css`\n      width: 100%;\n      height: 100vh;\n      display: flex;\n      background: ${token.colorBgContainer};\n      font-family: AlibabaPuHuiTi, ${token.fontFamily}, sans-serif;\n    `,\n    // side 样式\n    side: css`\n      background: ${token.colorBgLayout}80;\n      width: 280px;\n      height: 100%;\n      display: flex;\n      flex-direction: column;\n      padding: 0 12px;\n      box-sizing: border-box;\n    `,\n    logo: css`\n      display: flex;\n      align-items: center;\n      justify-content: start;\n      padding: 0 24px;\n      box-sizing: border-box;\n      gap: 8px;\n      margin: 24px 0;\n\n      span {\n        font-weight: bold;\n        color: ${token.colorText};\n        font-size: 16px;\n      }\n    `,\n    conversations: css`\n      overflow-y: auto;\n      margin-top: 12px;\n      padding: 0;\n      flex: 1;\n      .ant-conversations-list {\n        padding-inline-start: 0;\n      }\n    `,\n    sideFooter: css`\n      border-top: 1px solid ${token.colorBorderSecondary};\n      height: 40px;\n      display: flex;\n      align-items: center;\n      justify-content: space-between;\n    `,\n    // chat list 样式\n    chat: css`\n      height: 100%;\n      width: calc(100% - 280px);\n      box-sizing: border-box;\n      display: flex;\n      flex-direction: column;\n      padding-block: ${token.paddingLG}px;\n      justify-content: space-between;\n      .ant-bubble-content-updating {\n        background-image: linear-gradient(90deg, #ff6b23 0%, #af3cb8 31%, #53b6ff 89%);\n        background-size: 100% 2px;\n        background-repeat: no-repeat;\n        background-position: bottom;\n      }\n    `,\n    chatPrompt: css`\n      .ant-prompts-label {\n        color: #000000e0 !important;\n      }\n      .ant-prompts-desc {\n        color: #000000a6 !important;\n        width: 100%;\n      }\n      .ant-prompts-icon {\n        color: #000000a6 !important;\n      }\n    `,\n    chatList: css`\n      display: flex;\n      height: calc(100% - 120px);\n      flex-direction: column;\n      align-items: center;\n      width: 100%;\n    `,\n    placeholder: css`\n      padding-top: 32px;\n      width: 100%;\n      padding-inline: ${token.paddingLG}px;\n      box-sizing: border-box;\n    `,\n    // sender 样式\n    sender: css`\n      width: 100%;\n      max-width: 840px;\n    `,\n    speechButton: css`\n      font-size: 18px;\n      color: ${token.colorText} !important;\n    `,\n    senderPrompt: css`\n      width: 100%;\n      max-width: 840px;\n      margin: 0 auto;\n      color: ${token.colorText};\n    `,\n  };\n});\n\n// ==================== Static Config ====================\nconst HISTORY_MESSAGES: {\n  [key: string]: DefaultMessageInfo<ChatMessage>[];\n} = {\n  'default-1': [\n    {\n      message: { role: 'user', content: locale.howToQuicklyInstallAndImportComponents },\n      status: 'success',\n    },\n    {\n      message: {\n        role: 'assistant',\n        content: locale.aiMessage_2,\n      },\n      status: 'success',\n    },\n  ],\n  'default-2': [\n    { message: { role: 'user', content: locale.newAgiHybridInterface }, status: 'success' },\n    {\n      message: {\n        role: 'assistant',\n        content: locale.aiMessage_1,\n      },\n      status: 'success',\n    },\n  ],\n};\n\nconst DEFAULT_CONVERSATIONS_ITEMS = [\n  {\n    key: 'default-0',\n    label: locale.whatIsAntDesignX,\n    group: locale.today,\n  },\n  {\n    key: 'default-1',\n    label: locale.howToQuicklyInstallAndImportComponents,\n    group: locale.today,\n  },\n  {\n    key: 'default-2',\n    label: locale.newAgiHybridInterface,\n    group: locale.yesterday,\n  },\n];\n\nconst HOT_TOPICS = {\n  key: '1',\n  label: locale.hotTopics,\n  children: [\n    {\n      key: '1-1',\n      description: locale.whatComponentsAreInAntDesignX,\n      icon: <span style={{ color: '#f93a4a', fontWeight: 700 }}>1</span>,\n    },\n    {\n      key: '1-2',\n      description: locale.newAgiHybridInterface,\n      icon: <span style={{ color: '#ff6565', fontWeight: 700 }}>2</span>,\n    },\n    {\n      key: '1-3',\n      description: locale.whatComponentsAreInAntDesignX,\n      icon: <span style={{ color: '#ff8f1f', fontWeight: 700 }}>3</span>,\n    },\n    {\n      key: '1-4',\n      description: locale.comeAndDiscoverNewDesignParadigm,\n      icon: <span style={{ color: '#00000040', fontWeight: 700 }}>4</span>,\n    },\n    {\n      key: '1-5',\n      description: locale.howToQuicklyInstallAndImportComponents,\n      icon: <span style={{ color: '#00000040', fontWeight: 700 }}>5</span>,\n    },\n  ],\n};\n\nconst DESIGN_GUIDE = {\n  key: '2',\n  label: locale.designGuide,\n  children: [\n    {\n      key: '2-1',\n      icon: <HeartOutlined />,\n      label: locale.intention,\n      description: locale.aiUnderstandsUserNeedsAndProvidesSolutions,\n    },\n    {\n      key: '2-2',\n      icon: <SmileOutlined />,\n      label: locale.role,\n      description: locale.aiPublicPersonAndImage,\n    },\n    {\n      key: '2-3',\n      icon: <CommentOutlined />,\n      label: locale.chat,\n      description: locale.howAICanExpressItselfWayUsersUnderstand,\n    },\n    {\n      key: '2-4',\n      icon: <PaperClipOutlined />,\n      label: locale.interface,\n      description: locale.aiBalances,\n    },\n  ],\n};\n\nconst SENDER_PROMPTS: GetProp<typeof Prompts, 'items'> = [\n  {\n    key: '1',\n    description: locale.upgrades,\n    icon: <ScheduleOutlined />,\n  },\n  {\n    key: '2',\n    description: locale.components,\n    icon: <ProductOutlined />,\n  },\n  {\n    key: '3',\n    description: locale.richGuide,\n    icon: <FileSearchOutlined />,\n  },\n  {\n    key: '4',\n    description: locale.installationIntroduction,\n    icon: <AppstoreAddOutlined />,\n  },\n];\n\nconst THOUGHT_CHAIN_CONFIG = {\n  loading: {\n    title: locale.modelIsRunning,\n    status: 'loading',\n  },\n  updating: {\n    title: locale.modelIsRunning,\n    status: 'loading',\n  },\n  success: {\n    title: locale.modelExecutionCompleted,\n    status: 'success',\n  },\n  error: {\n    title: locale.executionFailed,\n    status: 'error',\n  },\n  abort: {\n    title: locale.aborted,\n    status: 'abort',\n  },\n};\n\n// ==================== Type ====================\ninterface ChatMessage extends XModelMessage {\n  extraInfo?: {\n    feedback: ActionsFeedbackProps['value'];\n  };\n}\n\n// ==================== Context ====================\nconst ChatContext = React.createContext<{\n  onReload?: ReturnType<typeof useXChat>['onReload'];\n  setMessage?: ReturnType<typeof useXChat<ChatMessage>>['setMessage'];\n}>({});\n\n// ==================== Sub Component ====================\n\nconst ThinkComponent = React.memo((props: ComponentProps) => {\n  const [title, setTitle] = React.useState(`${locale.deepThinking}...`);\n  const [loading, setLoading] = React.useState(true);\n\n  React.useEffect(() => {\n    if (props.streamStatus === 'done') {\n      setTitle(locale.completeThinking);\n      setLoading(false);\n    }\n  }, [props.streamStatus]);\n\n  return (\n    <Think title={title} loading={loading}>\n      {props.children}\n    </Think>\n  );\n});\n\nconst Footer: React.FC<{\n  id?: string | number;\n  content: string;\n  status?: string;\n  extraInfo?: ChatMessage['extraInfo'];\n}> = ({ id, content, extraInfo, status }) => {\n  const context = React.useContext(ChatContext);\n  const Items = [\n    {\n      key: 'pagination',\n      actionRender: <Pagination simple total={1} pageSize={1} />,\n    },\n    {\n      key: 'retry',\n      label: locale.retry,\n      icon: <SyncOutlined />,\n      onItemClick: () => {\n        if (id) {\n          context?.onReload?.(id, {\n            userAction: 'retry',\n          });\n        }\n      },\n    },\n    {\n      key: 'copy',\n      actionRender: <Actions.Copy text={content} />,\n    },\n    {\n      key: 'audio',\n      actionRender: (\n        <Actions.Audio\n          onClick={() => {\n            message.info(locale.isMock);\n          }}\n        />\n      ),\n    },\n    {\n      key: 'feedback',\n      actionRender: (\n        <Actions.Feedback\n          styles={{\n            liked: {\n              color: '#f759ab',\n            },\n          }}\n          value={extraInfo?.feedback || 'default'}\n          key=\"feedback\"\n          onChange={(val) => {\n            if (id) {\n              context?.setMessage?.(id, () => ({\n                extraInfo: {\n                  feedback: val,\n                },\n              }));\n              message.success(`${id}: ${val}`);\n            } else {\n              message.error('has no id!');\n            }\n          }}\n        />\n      ),\n    },\n  ];\n  return status !== 'updating' && status !== 'loading' ? (\n    <div style={{ display: 'flex' }}>{id && <Actions items={Items} />}</div>\n  ) : null;\n};\n\n// ==================== Chat Provider ====================\n/**\n * 🔔 Please replace the BASE_URL, MODEL with your own values.\n */\nconst providerCaches = new Map<string, DeepSeekChatProvider>();\nconst providerFactory = (conversationKey: string) => {\n  if (!providerCaches.get(conversationKey)) {\n    providerCaches.set(\n      conversationKey,\n      new DeepSeekChatProvider({\n        request: XRequest<XModelParams, Partial<Record<SSEFields, XModelResponse>>>(\n          'https://api.x.ant.design/api/big_model_glm-4.5-flash',\n          {\n            manual: true,\n            params: {\n              stream: true,\n              thinking: {\n                type: 'disabled',\n              },\n              model: 'glm-4.5-flash',\n            },\n          },\n        ),\n      }),\n    );\n  }\n  return providerCaches.get(conversationKey);\n};\n\nconst historyMessageFactory = (conversationKey: string): DefaultMessageInfo<ChatMessage>[] => {\n  return HISTORY_MESSAGES[conversationKey] || [];\n};\n\nconst getRole = (className: string): BubbleListProps['role'] => ({\n  assistant: {\n    placement: 'start',\n    header: (_, { status }) => {\n      const config = THOUGHT_CHAIN_CONFIG[status as keyof typeof THOUGHT_CHAIN_CONFIG];\n      return config ? (\n        <ThoughtChain.Item\n          style={{\n            marginBottom: 8,\n          }}\n          status={config.status as ThoughtChainItemProps['status']}\n          variant=\"solid\"\n          icon={<GlobalOutlined />}\n          title={config.title}\n        />\n      ) : null;\n    },\n    footer: (content, { status, key, extraInfo }) => (\n      <Footer\n        content={content}\n        status={status}\n        extraInfo={extraInfo as ChatMessage['extraInfo']}\n        id={key as string}\n      />\n    ),\n    contentRender: (content: any, { status }) => {\n      const newContent = content.replace('/\\n\\n/g', '<br/><br/>');\n      return (\n        <XMarkdown\n          paragraphTag=\"div\"\n          components={{\n            think: ThinkComponent,\n          }}\n          className={className}\n          streaming={{\n            hasNextChunk: status === 'updating',\n            enableAnimation: true,\n          }}\n        >\n          {newContent}\n        </XMarkdown>\n      );\n    },\n  },\n  user: { placement: 'end' },\n});\n\nconst Independent: React.FC = () => {\n  const { styles } = useStyle();\n  // ==================== State ====================\n\n  const {\n    conversations,\n    activeConversationKey,\n    setActiveConversationKey,\n    addConversation,\n    setConversations,\n  } = useXConversations({\n    defaultConversations: DEFAULT_CONVERSATIONS_ITEMS,\n    defaultActiveConversationKey: DEFAULT_CONVERSATIONS_ITEMS[0].key,\n  });\n\n  const [className] = useMarkdownTheme();\n  const [messageApi, contextHolder] = message.useMessage();\n  const [attachmentsOpen, setAttachmentsOpen] = useState(false);\n  const [attachedFiles, setAttachedFiles] = useState<GetProp<typeof Attachments, 'items'>>([]);\n\n  const [inputValue, setInputValue] = useState('');\n\n  // ==================== Runtime ====================\n\n  const { onRequest, messages, isRequesting, abort, onReload, setMessage } = useXChat<ChatMessage>({\n    provider: providerFactory(activeConversationKey), // every conversation has its own provider\n    conversationKey: activeConversationKey,\n    defaultMessages: historyMessageFactory(activeConversationKey),\n    requestPlaceholder: () => {\n      return {\n        content: locale.noData,\n        role: 'assistant',\n      };\n    },\n    requestFallback: (_, { messageInfo }) => {\n      return {\n        ...messageInfo?.message,\n        content: messageInfo?.message?.content || locale.requestFailedPleaseTryAgain,\n      };\n    },\n  });\n\n  // ==================== Event ====================\n  const onSubmit = (val: string) => {\n    if (!val) return;\n    onRequest({\n      messages: [{ role: 'user', content: val }],\n    });\n    setActiveConversationKey(activeConversationKey);\n  };\n\n  // ==================== Nodes ====================\n  const chatSide = (\n    <div className={styles.side}>\n      {/* 🌟 Logo */}\n      <div className={styles.logo}>\n        <img\n          src=\"https://mdn.alipayobjects.com/huamei_iwk9zp/afts/img/A*eco6RrQhxbMAAAAAAAAAAAAADgCCAQ/original\"\n          draggable={false}\n          alt=\"logo\"\n          width={24}\n          height={24}\n        />\n        <span>Ant Design X</span>\n      </div>\n      {/* 🌟 会话管理 */}\n      <Conversations\n        creation={{\n          onClick: () => {\n            if (messages.length === 0) {\n              messageApi.error(locale.itIsNowANewConversation);\n              return;\n            }\n            const now = dayjs().valueOf().toString();\n            addConversation({\n              key: now,\n              label: `${locale.newConversation} ${conversations.length + 1}`,\n              group: locale.today,\n            });\n            setActiveConversationKey(now);\n          },\n        }}\n        items={conversations.map(({ key, label, ...other }) => ({\n          key,\n          label: key === activeConversationKey ? `[${locale.curConversation}]${label}` : label,\n          ...other,\n        }))}\n        className={styles.conversations}\n        activeKey={activeConversationKey}\n        onActiveChange={setActiveConversationKey}\n        groupable\n        styles={{ item: { padding: '0 8px' } }}\n        menu={(conversation) => ({\n          items: [\n            {\n              label: locale.rename,\n              key: 'rename',\n              icon: <EditOutlined />,\n            },\n            {\n              label: locale.delete,\n              key: 'delete',\n              icon: <DeleteOutlined />,\n              danger: true,\n              onClick: () => {\n                const newList = conversations.filter((item) => item.key !== conversation.key);\n                const newKey = newList?.[0]?.key;\n                setConversations(newList);\n                if (conversation.key === activeConversationKey) {\n                  setActiveConversationKey(newKey);\n                }\n              },\n            },\n          ],\n        })}\n      />\n\n      <div className={styles.sideFooter}>\n        <Avatar size={24} />\n        <Button type=\"text\" icon={<QuestionCircleOutlined />} />\n      </div>\n    </div>\n  );\n\n  const chatList = (\n    <div className={styles.chatList}>\n      {messages?.length ? (\n        /* 🌟 消息列表 */\n        <Bubble.List\n          items={messages?.map((i) => ({\n            ...i.message,\n            key: i.id,\n            status: i.status,\n            loading: i.status === 'loading',\n            extraInfo: i.extraInfo,\n          }))}\n          styles={{\n            bubble: {\n              maxWidth: 840,\n            },\n          }}\n          role={getRole(className)}\n        />\n      ) : (\n        <Flex\n          vertical\n          style={{\n            maxWidth: 840,\n          }}\n          gap={16}\n          align=\"center\"\n          className={styles.placeholder}\n        >\n          <Welcome\n            style={{\n              width: '100%',\n            }}\n            variant=\"borderless\"\n            icon=\"https://mdn.alipayobjects.com/huamei_iwk9zp/afts/img/A*s5sNRo5LjfQAAAAAAAAAAAAADgCCAQ/fmt.webp\"\n            title={locale.welcome}\n            description={locale.welcomeDescription}\n            extra={\n              <Space>\n                <Button icon={<ShareAltOutlined />} />\n                <Button icon={<EllipsisOutlined />} />\n              </Space>\n            }\n          />\n          <Flex\n            gap={16}\n            justify=\"center\"\n            style={{\n              width: '100%',\n            }}\n          >\n            <Prompts\n              items={[HOT_TOPICS]}\n              styles={{\n                list: { height: '100%' },\n                item: {\n                  flex: 1,\n                  backgroundImage: 'linear-gradient(123deg, #e5f4ff 0%, #efe7ff 100%)',\n                  borderRadius: 12,\n                  border: 'none',\n                },\n                subItem: { padding: 0, background: 'transparent' },\n              }}\n              onItemClick={(info) => {\n                onSubmit(info.data.description as string);\n              }}\n              className={styles.chatPrompt}\n            />\n\n            <Prompts\n              items={[DESIGN_GUIDE]}\n              styles={{\n                item: {\n                  flex: 1,\n                  backgroundImage: 'linear-gradient(123deg, #e5f4ff 0%, #efe7ff 100%)',\n                  borderRadius: 12,\n                  border: 'none',\n                },\n                subItem: { background: '#ffffffa6' },\n              }}\n              onItemClick={(info) => {\n                onSubmit(info.data.description as string);\n              }}\n              className={styles.chatPrompt}\n            />\n          </Flex>\n        </Flex>\n      )}\n    </div>\n  );\n  const senderHeader = (\n    <Sender.Header\n      title={locale.uploadFile}\n      open={attachmentsOpen}\n      onOpenChange={setAttachmentsOpen}\n      styles={{ content: { padding: 0 } }}\n    >\n      <Attachments\n        beforeUpload={() => false}\n        items={attachedFiles}\n        onChange={(info) => setAttachedFiles(info.fileList)}\n        placeholder={(type) =>\n          type === 'drop'\n            ? { title: locale.dropFileHere }\n            : {\n                icon: <CloudUploadOutlined />,\n                title: locale.uploadFiles,\n                description: locale.clickOrDragFilesToUpload,\n              }\n        }\n      />\n    </Sender.Header>\n  );\n  const chatSender = (\n    <Flex\n      vertical\n      gap={12}\n      align=\"center\"\n      style={{\n        marginInline: 24,\n      }}\n    >\n      {/* 🌟 提示词 */}\n      {!attachmentsOpen && (\n        <Prompts\n          items={SENDER_PROMPTS}\n          onItemClick={(info) => {\n            onSubmit(info.data.description as string);\n          }}\n          styles={{\n            item: { padding: '6px 12px' },\n          }}\n          className={styles.senderPrompt}\n        />\n      )}\n      {/* 🌟 输入框 */}\n      <Sender\n        value={inputValue}\n        header={senderHeader}\n        onSubmit={() => {\n          onSubmit(inputValue);\n          setInputValue('');\n        }}\n        onChange={setInputValue}\n        onCancel={() => {\n          abort();\n        }}\n        prefix={\n          <Button\n            type=\"text\"\n            icon={<PaperClipOutlined style={{ fontSize: 18 }} />}\n            onClick={() => setAttachmentsOpen(!attachmentsOpen)}\n          />\n        }\n        loading={isRequesting}\n        className={styles.sender}\n        allowSpeech\n        placeholder={locale.askOrInputUseSkills}\n      />\n    </Flex>\n  );\n\n  // ==================== Render =================\n\n  return (\n    <XProvider locale={locale}>\n      <ChatContext.Provider value={{ onReload, setMessage }}>\n        {contextHolder}\n        <div className={styles.layout}>\n          {chatSide}\n          <div className={styles.chat}>\n            {chatList}\n            {chatSender}\n          </div>\n        </div>\n      </ChatContext.Provider>\n    </XProvider>\n  );\n};\n\nexport default Independent;\n";;
  