  export default "import { XMarkdown } from '@ant-design/x-markdown';\nimport { createStyles, css } from 'antd-style';\nimport React from 'react';\nimport '@ant-design/x-markdown/themes/light.css';\nimport { useIntl } from 'react-intl';\nimport { Adx_Markdown_En, Adx_Markdown_Zh } from '../_utils/adx-markdown';\n\nconst useStyles = createStyles(({ token }) => ({\n  customMarkdown: css`\n    /* 覆盖HTML标签样式 */\n    h1 {\n      color: ${token.colorPrimary};\n      font-size: 32px;\n      margin-bottom: 24px;\n      text-align: center;\n      position: relative;\n\n      &::after {\n        content: '';\n        position: absolute;\n        bottom: -8px;\n        left: 50%;\n        transform: translateX(-50%);\n        width: 60px;\n        height: 3px;\n        background: linear-gradient(90deg, ${token.colorPrimary}, ${token.colorPrimaryHover});\n        border-radius: 2px;\n      }\n    }\n\n    h2 {\n      color: ${token.colorTextHeading};\n      font-size: 24px;\n      margin: 32px 0 16px;\n      padding-left: 12px;\n      border-left: 4px solid ${token.colorPrimary};\n    }\n\n    h3 {\n      color: ${token.colorText};\n      font-size: 20px;\n      margin: 24px 0 12px;\n    }\n\n    p {\n      color: ${token.colorTextSecondary};\n      line-height: 1.8;\n      margin-bottom: 16px;\n    }\n\n    ul,\n    ol {\n      color: ${token.colorTextSecondary};\n      line-height: 1.8;\n      margin-bottom: 16px;\n\n      li {\n        margin-bottom: 8px;\n        position: relative;\n\n        &::before {\n          color: ${token.colorPrimary};\n          font-weight: bold;\n        }\n      }\n    }\n\n    ul li::before {\n      content: '• ';\n    }\n\n    ol li::before {\n      content: counter(list-item) '. ';\n    }\n  `,\n}));\n\nconst App = () => {\n  const { styles } = useStyles();\n  const { locale } = useIntl();\n  const content = locale === 'zh-CN' ? Adx_Markdown_Zh : Adx_Markdown_En;\n  return (\n    <div style={{ padding: 24 }}>\n      <XMarkdown content={content} className={styles.customMarkdown} />\n    </div>\n  );\n};\n\nexport default App;\n";;
  