
  import '/Users/dupeiyi/Project/private/x/packages/x/components/x-provider/demo/theme.tsx?watch=parent';
  export default "import {\n  CommentOutlined,\n  FireOutlined,\n  HeartOutlined,\n  OpenAIOutlined,\n  ReadOutlined,\n  RocketOutlined,\n  SmileOutlined,\n} from '@ant-design/icons';\nimport { Conversations, Prompts, PromptsProps, Sender, Suggestion, XProvider } from '@ant-design/x';\nimport { Card, ColorPicker, Divider, Flex, message, Slider, Space, Typography } from 'antd';\nimport React from 'react';\n\ntype ThemeData = {\n  colorPrimary: string;\n};\n\nconst defaultData: ThemeData = {\n  colorPrimary: '#d10eef',\n};\n\nconst renderTitle = (icon: React.ReactElement, title: string) => (\n  <Space align=\"start\">\n    {icon}\n    <span>{title}</span>\n  </Space>\n);\n\nconst items: PromptsProps['items'] = [\n  {\n    key: '1',\n    label: renderTitle(<FireOutlined style={{ color: '#FF4D4F' }} />, 'Hot Topics'),\n    description: 'What are you interested in?',\n    children: [\n      {\n        key: '1-1',\n        description: \"What's new in X?\",\n      },\n      {\n        key: '1-2',\n        description: \"What's AGI?\",\n      },\n      {\n        key: '1-3',\n        description: 'Where is the doc?',\n      },\n    ],\n  },\n  {\n    key: '2',\n    label: renderTitle(<ReadOutlined style={{ color: '#1890FF' }} />, 'Design Guide'),\n    description: 'How to design a good product?',\n    children: [\n      {\n        key: '2-1',\n        icon: <HeartOutlined />,\n        description: 'Know the well',\n      },\n      {\n        key: '2-2',\n        icon: <SmileOutlined />,\n        description: 'Set the AI role',\n      },\n      {\n        key: '2-3',\n        icon: <CommentOutlined />,\n        description: 'Express the feeling',\n      },\n    ],\n  },\n  {\n    key: '3',\n    label: renderTitle(<RocketOutlined style={{ color: '#722ED1' }} />, 'Start Creating'),\n    description: 'How to start a new project?',\n    children: [\n      {\n        key: '3-1',\n        label: 'Fast Start',\n        description: 'Install Ant Design X',\n      },\n      {\n        key: '3-2',\n        label: 'Online Playground',\n        description: 'Play on the web without installing',\n      },\n    ],\n  },\n];\n\nexport default () => {\n  const [data, setData] = React.useState<ThemeData>(defaultData);\n\n  return (\n    <>\n      <Flex gap={12} style={{ marginBottom: 16 }} align=\"center\">\n        <Typography.Text>ColorPrimary:</Typography.Text>\n        <ColorPicker\n          value={data.colorPrimary}\n          onChange={(value) => {\n            setData((origin) => ({ ...origin, colorPrimary: value.toHexString() }));\n          }}\n        />\n      </Flex>\n      <Card>\n        <XProvider\n          theme={{\n            token: data,\n          }}\n        >\n          <Flex style={{ height: 500 }} gap={12}>\n            <Conversations\n              style={{ width: 130 }}\n              creation={{\n                onClick: () => {},\n              }}\n              defaultActiveKey=\"1\"\n              items={[\n                {\n                  key: '1',\n                  label: 'Conversation - 1',\n                },\n                {\n                  key: '2',\n                  label: 'Conversation - 2',\n                },\n              ]}\n            />\n            <Divider orientation=\"vertical\" style={{ height: '100%' }} />\n            <Flex justify=\"space-between\" vertical style={{ flex: 1 }} gap={8}>\n              <Prompts\n                title=\"Do you want?\"\n                items={items}\n                wrap\n                styles={{\n                  list: {\n                    justifyContent: 'space-around',\n                    maxWidth: 1000,\n                    margin: '0 auto',\n                  },\n                  item: {\n                    flex: 'none',\n                    width: 'calc(30% - 6px)',\n                    backgroundImage: 'linear-gradient(137deg, #e5f4ff 0%, #efe7ff 100%)',\n                    border: 0,\n                  },\n                  subItem: {\n                    background: 'rgba(255,255,255,0.45)',\n                    border: '1px solid #FFF',\n                  },\n                }}\n                onItemClick={(info) => {\n                  message.success(`You clicked a prompt: ${info.data.key}`);\n                }}\n              />\n\n              <Suggestion items={[{ label: 'Write a report', value: 'report' }]}>\n                {({ onTrigger, onKeyDown }) => (\n                  <Sender\n                    autoSize={{\n                      maxRows: 3,\n                      minRows: 2,\n                    }}\n                    suffix={false}\n                    slotConfig={[\n                      { type: 'text', value: 'I want to go to' },\n                      {\n                        type: 'select',\n                        key: 'destination',\n                        props: {\n                          defaultValue: 'Beijing',\n                          options: ['Beijing', 'Shanghai', 'Guangzhou'],\n                          placeholder: 'Please select a destination',\n                        },\n                      },\n                      { type: 'text', value: 'for a trip with ' },\n                      { type: 'tag', key: 'tag', props: { label: '@ Chuck', value: 'a man' } },\n                      { type: 'text', value: ', the date is ' },\n                      {\n                        type: 'input',\n                        key: 'date',\n                        props: {\n                          placeholder: 'Please enter a date',\n                        },\n                      },\n                      { type: 'text', value: ', and the price should be ' },\n                      {\n                        type: 'custom',\n                        key: 'priceRange',\n                        props: {\n                          defaultValue: [20, 50],\n                        },\n                        customRender: (value: any, onChange: (value: any) => void, props) => {\n                          return (\n                            <div style={{ width: '100px' }}>\n                              <Slider\n                                {...props}\n                                style={{ margin: 0 }}\n                                range\n                                value={value}\n                                onChange={onChange}\n                              />\n                            </div>\n                          );\n                        },\n                        formatResult: (value: any) => {\n                          return `between ${value[0]} and ${value[1]} RMB.`;\n                        },\n                      },\n                    ]}\n                    onChange={(nextVal) => {\n                      if (nextVal === '/') {\n                        onTrigger();\n                      } else if (!nextVal) {\n                        onTrigger(false);\n                      }\n                    }}\n                    footer={(actions) => (\n                      <Flex justify=\"space-between\" align=\"center\">\n                        <Sender.Switch value={true} icon={<OpenAIOutlined />}>\n                          Deep Think\n                        </Sender.Switch>\n                        {actions}\n                      </Flex>\n                    )}\n                    onKeyDown={onKeyDown}\n                    placeholder='Type \"/\" to trigger suggestion'\n                  />\n                )}\n              </Suggestion>\n            </Flex>\n          </Flex>\n        </XProvider>\n      </Card>\n    </>\n  );\n};\n";;
  