  export default "import { DeleteOutlined, EditOutlined, ShareAltOutlined, StopOutlined } from '@ant-design/icons';\nimport type { ConversationsProps } from '@ant-design/x';\nimport { Conversations } from '@ant-design/x';\nimport type { GetProp } from 'antd';\nimport { theme } from 'antd';\nimport React from 'react';\n\nconst items: GetProp<ConversationsProps, 'items'> = Array.from({ length: 4 }).map((_, index) => ({\n  key: `item${index + 1}`,\n  label: `Conversation Item ${index + 1}`,\n  disabled: index === 3,\n}));\n\nconst App: React.FC = () => {\n  const { token } = theme.useToken();\n\n  const style = {\n    width: 256,\n    background: token.colorBgContainer,\n    borderRadius: token.borderRadius,\n  };\n\n  const menuConfig: ConversationsProps['menu'] = {\n    items: [\n      {\n        label: 'Rename',\n        key: 'Rename',\n        icon: <EditOutlined />,\n      },\n      {\n        label: 'Share',\n        key: 'Share',\n        icon: <ShareAltOutlined />,\n      },\n      {\n        type: 'divider',\n      },\n      {\n        label: 'Archive',\n        key: 'Archive',\n        icon: <StopOutlined />,\n        disabled: true,\n      },\n      {\n        label: 'Delete Chat',\n        key: 'deleteChat',\n        icon: <DeleteOutlined />,\n        danger: true,\n      },\n    ],\n    onClick: (itemInfo) => {\n      console.log(`Click ${itemInfo.key}`);\n      itemInfo.domEvent.stopPropagation();\n    },\n  };\n\n  return <Conversations defaultActiveKey=\"item1\" menu={menuConfig} items={items} style={style} />;\n};\n\nexport default App;\n";;
  