
  import '/Users/dupeiyi/Project/private/x/packages/x/components/sender/demo/basic.tsx?watch=parent';
  export default "import { Sender } from '@ant-design/x';\nimport { App, Flex } from 'antd';\nimport React, { useState } from 'react';\n\nconst Demo: React.FC = () => {\n  const [value, setValue] = useState<string>('Hello? this is X!');\n  const [loading, setLoading] = useState<boolean>(false);\n\n  const { message } = App.useApp();\n\n  // Mock send message\n  React.useEffect(() => {\n    if (loading) {\n      const timer = setTimeout(() => {\n        setLoading(false);\n        message.success('Send message successfully!');\n      }, 3000);\n      return () => {\n        clearTimeout(timer);\n      };\n    }\n  }, [loading]);\n\n  return (\n    <Flex vertical gap=\"middle\">\n      <Sender\n        loading={loading}\n        value={value}\n        onChange={(v) => {\n          setValue(v);\n        }}\n        onSubmit={() => {\n          setValue('');\n          setLoading(true);\n          message.info('Send message!');\n        }}\n        onCancel={() => {\n          setLoading(false);\n          message.error('Cancel sending!');\n        }}\n        autoSize={{ minRows: 4, maxRows: 6 }}\n      />\n      <Sender value=\"Force as loading\" loading readOnly autoSize={true} />\n      <Sender disabled value=\"Set to disabled\" />\n    </Flex>\n  );\n};\n\nexport default () => (\n  <App>\n    <Demo />\n  </App>\n);\n";;
  