  export default "import { FileCard, type FileCardListProps } from '@ant-design/x';\nimport { Flex, Segmented } from 'antd';\nimport React from 'react';\n\nconst App = () => {\n  const images = Array.from({ length: 11 }).map((_, index) => ({\n    name: `image-file-${index}.png`,\n    src: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',\n    byte: 1024,\n  }));\n  const files = [\n    {\n      name: 'excel-file.xlsx',\n      byte: 1024,\n    },\n    {\n      name: 'word-file.docx',\n      byte: 1024,\n    },\n    {\n      name: 'pdf-file.pdf',\n      byte: 1024,\n    },\n    {\n      name: 'ppt-file.pptx',\n      byte: 1024,\n    },\n    {\n      name: 'zip-file.zip',\n      byte: 1024,\n    },\n    {\n      name: 'txt-file.txt',\n      byte: 1024,\n    },\n  ];\n\n  const [overflow, setOverflow] = React.useState<FileCardListProps['overflow']>('wrap');\n\n  return (\n    <Flex vertical gap=\"middle\">\n      <Segmented\n        options={[\n          { value: 'wrap', label: 'Wrap' },\n          { value: 'scrollX', label: 'Scroll X' },\n          { value: 'scrollY', label: 'Scroll Y' },\n        ]}\n        value={overflow}\n        onChange={setOverflow}\n        style={{ marginInlineEnd: 'auto' }}\n      />\n      <FileCard.List items={[...images, ...files]} removable overflow={overflow} />\n    </Flex>\n  );\n};\n\nexport default App;\n";;
  