
  import '/Users/dupeiyi/Project/private/x/packages/x/components/sender/demo/speech-custom.tsx?watch=parent';
  export default "import { Sender } from '@ant-design/x';\nimport { App } from 'antd';\nimport React from 'react';\n\nconst Demo: React.FC = () => {\n  const { message } = App.useApp();\n  const [recording, setRecording] = React.useState(false);\n\n  return (\n    <Sender\n      onSubmit={() => {\n        message.success('Send message successfully!');\n      }}\n      allowSpeech={{\n        // When setting `recording`, the built-in speech recognition feature will be disabled\n        recording,\n        onRecordingChange: (nextRecording) => {\n          message.info(`Mock Customize Recording: ${nextRecording}`);\n          setRecording(nextRecording);\n        },\n      }}\n    />\n  );\n};\n\nexport default () => (\n  <App>\n    <Demo />\n  </App>\n);\n";;
  