
  import '/Users/dupeiyi/Project/private/x/packages/x/docs/x-markdown/demo/components/custom.tsx?watch=parent';
  export default "import { Bubble, Think } from '@ant-design/x';\nimport XMarkdown, { type ComponentProps } from '@ant-design/x-markdown';\nimport React, { useCallback, useEffect, useState } from 'react';\nimport '@ant-design/x-markdown/themes/light.css';\nimport {\n  DeleteOutlined,\n  DollarOutlined,\n  EyeOutlined,\n  PlusOutlined,\n  ShoppingCartOutlined,\n  UserOutlined,\n} from '@ant-design/icons';\nimport {\n  Button,\n  Card,\n  Flex,\n  Form,\n  Input,\n  Modal,\n  message,\n  Select,\n  Space,\n  Statistic,\n  Table,\n  Tag,\n} from 'antd';\n\nconst { Option } = Select;\n\n// 模拟业务数据\ninterface OrderData {\n  id: string;\n  customer: string;\n  product: string;\n  amount: number;\n  status: 'pending' | 'processing' | 'completed' | 'cancelled';\n  date: string;\n  region: string;\n}\n\ninterface SalesData {\n  name: string;\n  value: number;\n  color: string;\n}\n\n// 自定义业务组件 - 从模型数据获取的销售仪表板\nconst Salesdashboard = React.memo(({ children, streamStatus }: ComponentProps) => {\n  const [salesData, setSalesData] = useState<SalesData[]>([]);\n  const [totalSales, setTotalSales] = useState(0);\n  const [totalOrders, setTotalOrders] = useState(0);\n  const [newCustomers, setNewCustomers] = useState(0);\n\n  useEffect(() => {\n    if (children) {\n      // 从模型返回的数据中解析销售信息\n      try {\n        const parsedData = typeof children === 'string' ? JSON.parse(children) : children;\n\n        if (parsedData.sales) {\n          setSalesData(parsedData.sales);\n        }\n        if (parsedData.totalSales) {\n          setTotalSales(parsedData.totalSales);\n        }\n        if (parsedData.totalOrders) {\n          setTotalOrders(parsedData.totalOrders);\n        }\n        if (parsedData.newCustomers) {\n          setNewCustomers(parsedData.newCustomers);\n        }\n      } catch (_error) {\n        // 如果解析失败，使用默认数据\n        const defaultData = [\n          { name: '电子产品', value: 45000, color: '#3b82f6' },\n          { name: '服装', value: 32000, color: '#8b5cf6' },\n          { name: '家居用品', value: 28000, color: '#10b981' },\n        ];\n        setSalesData(defaultData);\n        setTotalSales(115000);\n        setTotalOrders(342);\n        setNewCustomers(67);\n      }\n    } else {\n      // 默认数据\n      const defaultData = [\n        { name: '电子产品', value: 45000, color: '#3b82f6' },\n        { name: '服装', value: 32000, color: '#8b5cf6' },\n        { name: '家居用品', value: 28000, color: '#10b981' },\n      ];\n      setSalesData(defaultData);\n      setTotalSales(115000);\n      setTotalOrders(342);\n      setNewCustomers(67);\n    }\n  }, [children]);\n\n  if (streamStatus === 'loading') return;\n  return (\n    <div style={{ padding: '20px' }}>\n      <Flex vertical gap=\"large\">\n        <Flex justify=\"space-between\" align=\"center\">\n          销售仪表板 (从模型数据获取)\n          <Tag color=\"blue\">实时数据</Tag>\n        </Flex>\n\n        <Flex gap=\"middle\" wrap>\n          <Card style={{ flex: 1, minWidth: 200 }}>\n            <Statistic\n              title=\"总销售额\"\n              value={totalSales}\n              prefix={<DollarOutlined />}\n              precision={2}\n              valueStyle={{ color: '#3f8600' }}\n            />\n          </Card>\n          <Card style={{ flex: 1, minWidth: 200 }}>\n            <Statistic\n              title=\"订单总数\"\n              value={totalOrders}\n              prefix={<ShoppingCartOutlined />}\n              valueStyle={{ color: '#1890ff' }}\n            />\n          </Card>\n          <Card style={{ flex: 1, minWidth: 200 }}>\n            <Statistic\n              title=\"新增客户\"\n              value={newCustomers}\n              prefix={<UserOutlined />}\n              valueStyle={{ color: '#722ed1' }}\n            />\n          </Card>\n        </Flex>\n\n        <Flex gap=\"large\" wrap>\n          <Card title=\"销售分布\" style={{ flex: 1, minWidth: 300 }}>\n            <div style={{ padding: '20px' }}>\n              {salesData.map((item, index) => (\n                <div key={index} style={{ marginBottom: 12 }}>\n                  <Flex justify=\"space-between\" align=\"center\">\n                    <span>{item.name}</span>\n                    <Tag color={item.color}>¥{item.value.toLocaleString()}</Tag>\n                  </Flex>\n                </div>\n              ))}\n            </div>\n          </Card>\n\n          <Card title=\"数据说明\" style={{ flex: 1, minWidth: 300 }}>\n            <div style={{ padding: '20px' }}>\n              <p>🤖 以上数据由AI模型实时生成</p>\n              <p>📊 数据格式: JSON格式，包含sales、totalSales、totalOrders、newCustomers字段</p>\n              <p>💡 示例格式: sales数组包含name和value字段</p>\n            </div>\n          </Card>\n        </Flex>\n      </Flex>\n    </div>\n  );\n});\n\n// 自定义业务组件 - 订单管理表格\nconst OrderManager = React.memo(() => {\n  const [orders, setOrders] = useState<OrderData[]>([]);\n  const [loading, setLoading] = useState(false);\n  const [modalVisible, setModalVisible] = useState(false);\n  const [editingOrder, setEditingOrder] = useState<OrderData | null>(null);\n  const [form] = Form.useForm();\n\n  const fetchOrders = useCallback(async () => {\n    setLoading(true);\n    try {\n      await new Promise((resolve) => setTimeout(resolve, 1000));\n\n      const mockOrders: OrderData[] = [\n        {\n          id: 'ORD001',\n          customer: '张三',\n          product: 'iPhone 15',\n          amount: 8999,\n          status: 'completed',\n          date: '2024-01-15',\n          region: '北京',\n        },\n        {\n          id: 'ORD002',\n          customer: '李四',\n          product: 'MacBook Pro',\n          amount: 15999,\n          status: 'processing',\n          date: '2024-01-16',\n          region: '上海',\n        },\n        {\n          id: 'ORD003',\n          customer: '王五',\n          product: 'AirPods Pro',\n          amount: 1999,\n          status: 'pending',\n          date: '2024-01-17',\n          region: '广州',\n        },\n        {\n          id: 'ORD004',\n          customer: '赵六',\n          product: 'iPad Air',\n          amount: 4799,\n          status: 'completed',\n          date: '2024-01-18',\n          region: '深圳',\n        },\n      ];\n\n      setOrders(mockOrders);\n    } catch (_error) {\n      message.error('获取订单失败');\n    } finally {\n      setLoading(false);\n    }\n  }, []);\n\n  useEffect(() => {\n    fetchOrders();\n  }, [fetchOrders]);\n\n  const handleDelete = async (id: string) => {\n    try {\n      await new Promise((resolve) => setTimeout(resolve, 500));\n      setOrders((prev) => prev.filter((order) => order.id !== id));\n      message.success('订单已删除');\n    } catch (_error) {\n      message.error('删除失败');\n    }\n  };\n\n  const handleEdit = (order: OrderData) => {\n    setEditingOrder(order);\n    form.setFieldsValue(order);\n    setModalVisible(true);\n  };\n\n  const handleSubmit = async (values: Partial<OrderData>) => {\n    try {\n      await new Promise((resolve) => setTimeout(resolve, 800));\n\n      if (editingOrder) {\n        setOrders((prev) =>\n          prev.map((order) => (order.id === editingOrder.id ? { ...order, ...values } : order)),\n        );\n        message.success('订单已更新');\n      } else {\n        const newOrder: OrderData = {\n          id: `ORD${String(Date.now()).slice(-3)}`,\n          customer: values.customer || '',\n          product: values.product || '',\n          amount: values.amount || 0,\n          status: values.status || 'pending',\n          date: new Date().toISOString().split('T')[0],\n          region: values.region || '',\n        };\n        setOrders((prev) => [...prev, newOrder]);\n        message.success('订单已创建');\n      }\n\n      setModalVisible(false);\n      form.resetFields();\n      setEditingOrder(null);\n    } catch (_error) {\n      message.error('操作失败');\n    }\n  };\n\n  const columns = [\n    { title: '订单号', dataIndex: 'id', key: 'id' },\n    { title: '客户', dataIndex: 'customer', key: 'customer' },\n    { title: '产品', dataIndex: 'product', key: 'product' },\n    { title: '金额', dataIndex: 'amount', key: 'amount', render: (amount: number) => `¥${amount}` },\n    {\n      title: '状态',\n      dataIndex: 'status',\n      key: 'status',\n      render: (status: string) => {\n        const colors = {\n          pending: 'orange',\n          processing: 'blue',\n          completed: 'green',\n          cancelled: 'red',\n        };\n        const labels = {\n          pending: '待处理',\n          processing: '处理中',\n          completed: '已完成',\n          cancelled: '已取消',\n        };\n        return (\n          <Tag color={colors[status as keyof typeof colors]}>\n            {labels[status as keyof typeof labels]}\n          </Tag>\n        );\n      },\n    },\n    { title: '日期', dataIndex: 'date', key: 'date' },\n    { title: '地区', dataIndex: 'region', key: 'region' },\n    {\n      title: '操作',\n      key: 'action',\n      render: (_: any, record: OrderData) => (\n        <Space>\n          <Button type=\"link\" icon={<EyeOutlined />} onClick={() => handleEdit(record)} />\n          <Button\n            type=\"link\"\n            danger\n            icon={<DeleteOutlined />}\n            onClick={() => handleDelete(record.id)}\n          />\n        </Space>\n      ),\n    },\n  ];\n\n  return (\n    <div style={{ padding: '20px' }}>\n      <Flex vertical gap=\"middle\">\n        <Flex justify=\"space-between\" align=\"center\">\n          <h2>订单管理</h2>\n          <Button type=\"primary\" icon={<PlusOutlined />} onClick={() => setModalVisible(true)}>\n            新建订单\n          </Button>\n        </Flex>\n\n        <Table\n          columns={columns}\n          dataSource={orders}\n          loading={loading}\n          rowKey=\"id\"\n          pagination={{ pageSize: 5 }}\n        />\n\n        <Modal\n          title={editingOrder ? '编辑订单' : '新建订单'}\n          open={modalVisible}\n          onCancel={() => {\n            setModalVisible(false);\n            form.resetFields();\n            setEditingOrder(null);\n          }}\n          onOk={() => form.submit()}\n        >\n          <Form form={form} onFinish={handleSubmit} layout=\"vertical\">\n            <Form.Item name=\"customer\" label=\"客户名称\" rules={[{ required: true }]}>\n              <Input />\n            </Form.Item>\n            <Form.Item name=\"product\" label=\"产品名称\" rules={[{ required: true }]}>\n              <Input />\n            </Form.Item>\n            <Form.Item name=\"amount\" label=\"金额\" rules={[{ required: true }]}>\n              <Input type=\"number\" />\n            </Form.Item>\n            <Form.Item name=\"status\" label=\"状态\" rules={[{ required: true }]}>\n              <Select>\n                <Option value=\"pending\">待处理</Option>\n                <Option value=\"processing\">处理中</Option>\n                <Option value=\"completed\">已完成</Option>\n                <Option value=\"cancelled\">已取消</Option>\n              </Select>\n            </Form.Item>\n            <Form.Item name=\"region\" label=\"地区\" rules={[{ required: true }]}>\n              <Input />\n            </Form.Item>\n          </Form>\n        </Modal>\n      </Flex>\n    </div>\n  );\n});\n\n// 思考组件\nconst ThinkComponent = React.memo((props: ComponentProps) => {\n  const [title, setTitle] = React.useState('正在分析业务数据...');\n  const [loading, setLoading] = React.useState(true);\n  const [expand, setExpand] = React.useState(true);\n\n  React.useEffect(() => {\n    if (props.streamStatus === 'done') {\n      setTitle('业务分析完成');\n      setLoading(false);\n      setExpand(false);\n    }\n  }, [props.streamStatus]);\n\n  if (typeof props.children !== 'string') {\n    return null;\n  }\n\n  return (\n    <div style={{ padding: '12px 0' }}>\n      <Think title={title} loading={loading} expanded={expand} onClick={() => setExpand(!expand)}>\n        {props?.children?.trim()}\n      </Think>\n    </div>\n  );\n});\n\nconst text = `\n<think>\n基于用户提供的业务需求，我们需要创建一个完整的销售管理系统示例，该系统需要展示如何从AI模型返回的数据中动态获取和展示信息。这个示例将展示XMarkdown如何：\n1. 从模型返回的JSON数据中解析业务信息\n2. 使用小写组件标签（如salesdashboard）\n3. 处理动态数据渲染\n4. 实现复杂的业务场景和交互需求\n通过这种方式，用户可以清楚地看到XMarkdown不仅支持简单的文本渲染，还能处理动态数据驱动的复杂业务场景。\n</think>\n\n### 📊 动态销售仪表板\n\n<salesdashboard>{\"sales\":[{\"name\":\"电子产品\",\"value\":52000,\"color\":\"#3b82f6\"},{\"name\":\"服装\",\"value\":38000,\"color\":\"#8b5cf6\"}],\"totalSales\":141000,\"totalOrders\":487,\"newCustomers\":94}</salesdashboard>\n\n### 📋 订单管理系统\n\n<ordermanager />\n`;\n\nconst App = () => {\n  const [index, setIndex] = React.useState(0);\n  const timer = React.useRef<any>(-1);\n\n  const renderStream = () => {\n    if (index >= text.length) {\n      clearTimeout(timer.current);\n      return;\n    }\n    timer.current = setTimeout(() => {\n      setIndex((prev) => prev + 10);\n      renderStream();\n    }, 30);\n  };\n\n  React.useEffect(() => {\n    if (index === text.length) return;\n    renderStream();\n    return () => {\n      clearTimeout(timer.current);\n    };\n  }, [index]);\n\n  return (\n    <Flex vertical gap=\"small\" style={{ maxWidth: 1200, margin: '0 auto', padding: 20 }}>\n      <Button style={{ alignSelf: 'flex-end' }} onClick={() => setIndex(0)}>\n        Re-render\n      </Button>\n\n      <Bubble\n        content={text.slice(0, index)}\n        contentRender={(content) => (\n          <XMarkdown\n            components={{\n              think: ThinkComponent,\n              salesdashboard: Salesdashboard,\n              ordermanager: OrderManager,\n            }}\n            paragraphTag=\"div\"\n          >\n            {content}\n          </XMarkdown>\n        )}\n        variant=\"outlined\"\n      />\n    </Flex>\n  );\n};\n\nexport default App;\n";;
  