
  import '/Users/dupeiyi/Project/private/x/packages/x/components/conversations/demo/shortcutKeys.tsx?watch=parent';
  export default "import {\n  CodeOutlined,\n  CodeSandboxOutlined,\n  FileImageOutlined,\n  FileSearchOutlined,\n  SignatureOutlined,\n} from '@ant-design/icons';\nimport { Conversations, ConversationsProps } from '@ant-design/x';\nimport type { GetProp } from 'antd';\nimport { Flex, Tag, theme } from 'antd';\nimport KeyCode from 'rc-util/lib/KeyCode';\nimport React, { useState } from 'react';\n\nconst agentItems: GetProp<ConversationsProps, 'items'> = [\n  {\n    key: 'write',\n    label: 'Help Me Write',\n    icon: <SignatureOutlined />,\n  },\n  {\n    key: 'coding',\n    label: 'AI Coding',\n    icon: <CodeOutlined />,\n  },\n  {\n    key: 'createImage',\n    label: 'Create Image',\n    icon: <FileImageOutlined />,\n  },\n  {\n    key: 'deepSearch',\n    label: 'Deep Search',\n    icon: <FileSearchOutlined />,\n  },\n\n  {\n    key: 'inDepthResearch',\n    label: 'In-depth research',\n    group: 'More Features',\n  },\n  {\n    key: 'vincentFigure',\n    label: 'Vincent Figure',\n    group: 'More Features',\n  },\n  {\n    type: 'divider',\n  },\n];\n\nconst conversationsText = (\n  <div style={{ marginBottom: 16 }}>\n    You can switch sessions using the shortcut key: <Tag>Alt/⌥</Tag> + <Tag>number</Tag>, and create\n    new chat using the shortcut key: <Tag>Win/⌘</Tag> + <Tag>K</Tag>.\n  </div>\n);\n\nconst App: React.FC = () => {\n  const { token } = theme.useToken();\n  // Customize the style of the container\n  const style = {\n    width: 256,\n    background: token.colorBgContainer,\n    borderRadius: token.borderRadius,\n  };\n\n  const [historicalItems, setHistoricalItems] = useState<GetProp<ConversationsProps, 'items'>>([\n    {\n      key: `item1`,\n      label: 'Conversation Item 1',\n      group: 'Today',\n    },\n  ]);\n\n  const items = [...agentItems, ...historicalItems];\n\n  const newChatClick = () => {\n    setHistoricalItems((ori) => {\n      return [\n        ...ori,\n        {\n          key: `item${ori.length + 1}`,\n          label: `Conversation Item ${ori.length + 1}`,\n          group: 'Today',\n        },\n      ];\n    });\n  };\n\n  return (\n    <>\n      {conversationsText}\n      <Conversations\n        creation={{\n          onClick: newChatClick,\n        }}\n        style={style}\n        defaultActiveKey=\"write\"\n        onActiveChange={(value) => {\n          console.log(value);\n        }}\n        shortcutKeys={{\n          creation: ['Meta', KeyCode.K],\n          items: ['Alt', 'number'],\n        }}\n        groupable={{\n          label: (group) => {\n            return group !== 'Today' ? (\n              <Flex gap=\"small\">\n                <CodeSandboxOutlined />\n                {group}\n              </Flex>\n            ) : (\n              group\n            );\n          },\n          collapsible: (group) => {\n            return group !== 'Today';\n          },\n        }}\n        items={items}\n      />\n    </>\n  );\n};\n\nexport default App;\n";;
  