
  import '/Users/dupeiyi/Project/private/x/packages/x/docs/x-sdk/demos/x-request/custom-params.tsx?watch=parent';
  export default "import { LoadingOutlined, TagsOutlined } from '@ant-design/icons';\nimport type { ThoughtChainItemType } from '@ant-design/x';\nimport { ThoughtChain } from '@ant-design/x';\nimport { XRequest } from '@ant-design/x-sdk';\nimport { Button, Descriptions, Splitter } from 'antd';\nimport React from 'react';\n\n/**\n * 🔔 Please replace the BASE_URL, PATH, with your own values.\n */\nconst BASE_URL = 'https://api.example.com';\nconst PATH = '/chat';\n\nconst App = () => {\n  const [status, setStatus] = React.useState<ThoughtChainItemType['status']>();\n  const [lines, setLines] = React.useState<Record<string, string>[]>([]);\n\n  function request() {\n    setStatus('loading');\n\n    XRequest(BASE_URL + PATH, {\n      params: {\n        model: 'gpt-3.5-turbo',\n        messages: [{ role: 'user', content: 'hello, who are u?' }],\n        stream: true,\n        agentId: 111,\n      },\n      callbacks: {\n        onSuccess: (messages) => {\n          setStatus('success');\n          console.log('onSuccess', messages);\n        },\n        onError: (error) => {\n          setStatus('error');\n          console.error('onError', error);\n        },\n        onUpdate: (msg) => {\n          setLines((pre) => [...pre, msg]);\n          console.log('onUpdate', msg);\n        },\n      },\n    });\n  }\n\n  return (\n    <Splitter>\n      <Splitter.Panel>\n        <Button type=\"primary\" disabled={status === 'loading'} onClick={request}>\n          Request - {BASE_URL}\n          {PATH}\n        </Button>\n      </Splitter.Panel>\n      <Splitter.Panel style={{ marginLeft: 16 }}>\n        <ThoughtChain\n          items={[\n            {\n              title: 'Request Log',\n              status: status,\n              icon: status === 'loading' ? <LoadingOutlined /> : <TagsOutlined />,\n              description:\n                status === 'error' &&\n                'Please replace the BASE_URL, PATH and parameters, with your own values.',\n              content: (\n                <Descriptions column={1}>\n                  <Descriptions.Item label=\"Status\">{status || '-'}</Descriptions.Item>\n                  <Descriptions.Item label=\"Update Times\">{lines.length}</Descriptions.Item>\n                </Descriptions>\n              ),\n            },\n          ]}\n        />\n      </Splitter.Panel>\n    </Splitter>\n  );\n};\n\nexport default App;\n";;
  