  export default "import { FileCard } from '@ant-design/x';\nimport { Button, Flex, Space } from 'antd';\nimport React, { useState } from 'react';\n\nconst App = () => {\n  const [loading, setLoading] = useState(true);\n  const [src, setSrc] = useState('');\n\n  return (\n    <Flex gap=\"middle\" vertical>\n      <Space>\n        <Button\n          disabled={loading}\n          onClick={() => {\n            setLoading(true);\n            setSrc('');\n          }}\n        >\n          Start Loading\n        </Button>\n        <Button\n          onClick={() => {\n            setSrc(`https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png`);\n            const timer = setTimeout(() => {\n              timer && clearTimeout(timer);\n              setLoading(false);\n            }, 400);\n          }}\n        >\n          Load Complete\n        </Button>\n      </Space>\n\n      <FileCard\n        loading={loading}\n        styles={{\n          file: {\n            width: 200,\n          },\n        }}\n        spinProps={{\n          size: 'small',\n        }}\n        imageProps={{\n          placeholder: (\n            <FileCard\n              imageProps={{\n                alt: 'placeholder image',\n                preview: false,\n              }}\n              name=\"image-file-placeholder.png\"\n              src={\n                src\n                  ? `${src}?x-oss-process=image/blur,r_50,s_50/quality,q_1/resize,m_mfit,h_200,w_200`\n                  : ''\n              }\n            />\n          ),\n        }}\n        name=\"image-file.png\"\n        src={src ? `${src}?${Date.now()}` : ''}\n      />\n      <FileCard\n        loading={loading}\n        imageProps={{\n          placeholder: (\n            <FileCard\n              imageProps={{\n                alt: 'placeholder image',\n                preview: false,\n              }}\n              name=\"image-file-placeholder.png\"\n              src={\n                src\n                  ? `${src}?x-oss-process=image/blur,r_50,s_50/quality,q_1/resize,m_mfit,h_200,w_200`\n                  : ''\n              }\n            />\n          ),\n        }}\n        name=\"image-file.png\"\n        src={src ? `${src}?${Date.now()}` : ''}\n      />\n    </Flex>\n  );\n};\n\nexport default App;\n";;
  