
  import '/Users/dupeiyi/Project/private/x/packages/x/docs/x-markdown/demo/supersets/Mermaid/basic.tsx?watch=parent';
  export default "import { Bubble } from '@ant-design/x';\nimport XMarkdown, { type ComponentProps } from '@ant-design/x-markdown';\nimport Mermaid from '@ant-design/x-markdown/plugins/Mermaid';\nimport { Button, Flex } from 'antd';\nimport React from 'react';\n\nconst text = `\nHere are several Mermaid diagram examples \n\n#### 1. Flowchart (Vertical)\n\n\\`\\`\\` mermaid\ngraph TD\n    A[Start] --> B{Data Valid?}\n    B -->|Yes| C[Process Data]\n    B -->|No| D[Error Handling]\n    C --> E[Generate Report]\n    D --> E\n    E --> F[End]\n    style A fill:#2ecc71,stroke:#27ae60\n    style F fill:#e74c3c,stroke:#c0392b\n\\`\\`\\`\n\n#### 2. Sequence Diagram\n\n\\`\\`\\` mermaid\nsequenceDiagram\n    participant Client\n    participant Server\n    participant Database\n    \n    Client->>Server: POST /api/data\n    Server->>Database: INSERT record\n    Database-->>Server: Success\n    Server-->>Client: 201 Created\n\\`\\`\\`\n\n#### 3. Quadrant Chart\n\n\\`\\`\\`mermaid\nquadrantChart\n    title Reach and engagement of campaigns\n    x-axis Low Reach --> High Reach\n    y-axis Low Engagement --> High Engagement\n    quadrant-1 We should expand\n    quadrant-2 Need to promote\n    quadrant-3 Re-evaluate\n    quadrant-4 May be improved\n    Campaign A: [0.3, 0.6]\n    Campaign B: [0.45, 0.23]\n    Campaign C: [0.57, 0.69]\n    Campaign D: [0.78, 0.34]\n    Campaign E: [0.40, 0.34]\n    Campaign F: [0.35, 0.78]\n\\`\\`\\`\n`;\n\nconst Code: React.FC<ComponentProps> = (props) => {\n  const { className, children } = props;\n  const lang = className?.match(/language-(\\w+)/)?.[1] || '';\n\n  if (typeof children !== 'string') return null;\n  if (lang === 'mermaid') {\n    return <Mermaid>{children}</Mermaid>;\n  }\n  return <code>{children}</code>;\n};\n\nconst App = () => {\n  const [index, setIndex] = React.useState(0);\n  const timer = React.useRef<any>(-1);\n\n  const renderStream = () => {\n    if (index >= text.length) {\n      clearTimeout(timer.current);\n      return;\n    }\n    timer.current = setTimeout(() => {\n      setIndex((prev) => prev + 5);\n      renderStream();\n    }, 20);\n  };\n\n  React.useEffect(() => {\n    if (index === text.length) return;\n    renderStream();\n    return () => {\n      clearTimeout(timer.current);\n    };\n  }, [index]);\n\n  return (\n    <Flex vertical gap=\"small\">\n      <Button style={{ alignSelf: 'flex-end' }} onClick={() => setIndex(0)}>\n        Re-Render\n      </Button>\n\n      <Bubble\n        content={text.slice(0, index)}\n        styles={{\n          content: {\n            width: 700,\n          },\n        }}\n        contentRender={(content) => (\n          <XMarkdown components={{ code: Code }} paragraphTag=\"div\">\n            {content}\n          </XMarkdown>\n        )}\n        variant=\"outlined\"\n      />\n    </Flex>\n  );\n};\n\nexport default App;\n";;
  