  export default "import { CheckCircleOutlined } from '@ant-design/icons';\nimport type { ThoughtChainItemType, ThoughtChainProps } from '@ant-design/x';\nimport { ThoughtChain } from '@ant-design/x';\nimport { Button } from 'antd';\nimport React from 'react';\nimport SemanticPreview from '../../../.dumi/components/SemanticPreview';\nimport useLocale from '../../../.dumi/hooks/useLocale';\n\nconst customizationProps: ThoughtChainItemType = {\n  title: 'Thought Chain Item Title',\n  description: 'description',\n  icon: <CheckCircleOutlined />,\n  footer: <Button block>Thought Chain Item Footer</Button>,\n  content: 'Thought Chain Item Content',\n};\n\nconst items: ThoughtChainProps['items'] = [\n  {\n    ...customizationProps,\n    status: 'success',\n  },\n];\n\nconst locales = {\n  cn: {\n    root: '根节点',\n    item: '思维链节点',\n    itemIcon: '思维链图标',\n    itemHeader: '思维链节点头部',\n    itemContent: '思维链节点内容',\n    itemFooter: '思维链节点页脚',\n  },\n  en: {\n    root: 'Root',\n    item: 'Item',\n    itemIcon: 'Item Icon',\n    itemHeader: 'Item Header',\n    itemContent: 'Item Content',\n    itemFooter: 'Item Footer',\n  },\n};\n\nconst App: React.FC = () => {\n  const [locale] = useLocale(locales);\n  return (\n    <SemanticPreview\n      componentName=\"ThoughtChain\"\n      semantics={[\n        {\n          name: 'root',\n          desc: locale.root,\n        },\n        {\n          name: 'item',\n          desc: locale.item,\n        },\n        {\n          name: 'itemIcon',\n          desc: locale.itemIcon,\n        },\n        {\n          name: 'itemHeader',\n          desc: locale.itemHeader,\n        },\n        {\n          name: 'itemContent',\n          desc: locale.itemContent,\n        },\n        {\n          name: 'itemFooter',\n          desc: locale.itemFooter,\n        },\n      ]}\n    >\n      <ThoughtChain items={items} />\n    </SemanticPreview>\n  );\n};\n\nexport default App;\n";;
  