  export default "import { DeleteOutlined, EditOutlined, RedoOutlined, ShareAltOutlined } from '@ant-design/icons';\nimport { Actions, ActionsProps } from '@ant-design/x';\nimport { Modal, message } from 'antd';\nimport React from 'react';\n\nconst actionItems: ActionsProps['items'] = [\n  {\n    key: 'retry',\n    label: 'Retry',\n    icon: <RedoOutlined />,\n  },\n  {\n    key: 'edit',\n    icon: <EditOutlined />,\n    label: 'Edit',\n  },\n  {\n    key: 'more',\n    subItems: [\n      {\n        key: 'share',\n        label: 'Share',\n        icon: <ShareAltOutlined />,\n      },\n      { key: 'import', label: 'Import' },\n      {\n        key: 'delete',\n        label: 'Delete',\n        icon: <DeleteOutlined />,\n        onItemClick: () => {\n          Modal.confirm({\n            title: 'Are you sure want to delete?',\n            content: 'Some descriptions',\n            onOk() {\n              message.success('Delete successfully');\n            },\n            onCancel() {\n              message.info('Cancel');\n            },\n          });\n        },\n        danger: true,\n      },\n    ],\n  },\n  {\n    key: 'clear',\n    label: 'Clear',\n    icon: <DeleteOutlined />,\n    danger: true,\n  },\n];\n\nconst App: React.FC = () => {\n  const onClick: ActionsProps['onClick'] = ({ keyPath }) => {\n    // Logic for handling click events\n    message.success(`you clicked ${keyPath.join(',')}`);\n  };\n  return <Actions items={actionItems} onClick={onClick} />;\n};\n\nexport default App;\n";;
  