
  import '/Users/dupeiyi/Project/private/x/packages/x/components/attachments/demo/with-sender.tsx?watch=parent';
  export default "import { CloudUploadOutlined, LinkOutlined } from '@ant-design/icons';\nimport { Attachments, AttachmentsProps, Sender } from '@ant-design/x';\nimport { App, Badge, Button, Flex, type GetProp, type GetRef, Typography } from 'antd';\nimport React from 'react';\n\nconst Demo = () => {\n  const [open, setOpen] = React.useState(true);\n  const [items, setItems] = React.useState<GetProp<AttachmentsProps, 'items'>>([]);\n  const [text, setText] = React.useState('');\n\n  const { notification } = App.useApp();\n\n  const senderRef = React.useRef<GetRef<typeof Sender>>(null);\n\n  React.useEffect(() => {\n    // Clear all created object URLs when the component is unmounted\n    return () => {\n      items.forEach((item) => {\n        if (item.url?.startsWith('blob:')) {\n          URL.revokeObjectURL(item.url);\n        }\n      });\n    };\n  }, []);\n\n  const senderHeader = (\n    <Sender.Header\n      title=\"Attachments\"\n      open={open}\n      onOpenChange={setOpen}\n      styles={{\n        content: {\n          padding: 0,\n        },\n      }}\n    >\n      <Attachments\n        // Mock not real upload file\n        beforeUpload={() => false}\n        items={items}\n        onChange={({ file, fileList }) => {\n          const updatedFileList = fileList.map((item) => {\n            if (item.uid === file.uid && file.status !== 'removed' && item.originFileObj) {\n              // clear URL\n              if (item.url?.startsWith('blob:')) {\n                URL.revokeObjectURL(item.url);\n              }\n              // create new preview URL\n              return {\n                ...item,\n                url: URL.createObjectURL(item.originFileObj),\n              };\n            }\n            return item;\n          });\n          setItems(updatedFileList);\n        }}\n        placeholder={(type) =>\n          type === 'drop'\n            ? {\n                title: 'Drop file here',\n              }\n            : {\n                icon: <CloudUploadOutlined />,\n                title: 'Upload files',\n                description: 'Click or drag files to this area to upload',\n              }\n        }\n        getDropContainer={() => senderRef.current?.nativeElement}\n      />\n    </Sender.Header>\n  );\n\n  return (\n    <Flex style={{ minHeight: 250 }} align=\"flex-end\">\n      <Sender\n        ref={senderRef}\n        header={senderHeader}\n        prefix={\n          <Badge dot={items.length > 0 && !open}>\n            <Button onClick={() => setOpen(!open)} icon={<LinkOutlined />} />\n          </Badge>\n        }\n        value={text}\n        onChange={setText}\n        onSubmit={() => {\n          notification.info({\n            title: 'Mock Submit',\n            description: (\n              <Typography>\n                <ul>\n                  <li>You said: {text}</li>\n                  <li>\n                    Attachments count: {items.length}\n                    <ul>\n                      {items.map((item) => (\n                        <li key={item.uid}>{item.name}</li>\n                      ))}\n                    </ul>\n                  </li>\n                </ul>\n              </Typography>\n            ),\n          });\n\n          setItems([]);\n          setText('');\n        }}\n      />\n    </Flex>\n  );\n};\n\nexport default () => (\n  <App>\n    <Demo />\n  </App>\n);\n";;
  