
  import '/Users/dupeiyi/Project/private/x/packages/x/components/bubble/demo/semantic-list-custom.tsx?watch=parent';
  export default "import { FrownOutlined, SmileOutlined, SyncOutlined } from '@ant-design/icons';\nimport { Bubble } from '@ant-design/x';\nimport type { GetProp, GetRef } from 'antd';\nimport { Button, Flex, Spin } from 'antd';\nimport React from 'react';\n\nconst roles: GetProp<typeof Bubble.List, 'role'> = {\n  ai: {\n    placement: 'start',\n    typing: { effect: 'typing', step: 5, interval: 20 },\n    loadingRender: () => (\n      <Flex align=\"center\" gap=\"small\">\n        <Spin size=\"small\" />\n        Custom loading...\n      </Flex>\n    ),\n  },\n  user: {\n    placement: 'end',\n  },\n};\n\nconst App = () => {\n  const listRef = React.useRef<GetRef<typeof Bubble.List>>(null);\n  return (\n    <Bubble.List\n      ref={listRef}\n      style={{ height: 500 }}\n      role={roles}\n      items={[\n        {\n          key: 'welcome',\n          role: 'ai',\n          content: 'Mock welcome content. '.repeat(10),\n          footer: (\n            <Flex>\n              <Button\n                size=\"small\"\n                type=\"text\"\n                icon={<SyncOutlined />}\n                style={{ marginInlineEnd: 'auto' }}\n              />\n              <Button size=\"small\" type=\"text\" icon={<SmileOutlined />} />\n              <Button size=\"small\" type=\"text\" icon={<FrownOutlined />} />\n            </Flex>\n          ),\n        },\n        {\n          key: 'ask',\n          role: 'user',\n          content: 'Mock user content.',\n        },\n        {\n          key: 'ai',\n          role: 'ai',\n          loading: true,\n          content: '',\n        },\n      ]}\n    />\n  );\n};\n\nexport default App;\n";;
  