
  import '/Users/dupeiyi/Project/private/x/packages/x/components/bubble/demo/editable.tsx?watch=parent';
  export default "import { EditOutlined, UserOutlined } from '@ant-design/icons';\nimport { Actions, Bubble } from '@ant-design/x';\nimport { Avatar, Flex } from 'antd';\nimport React, { useState } from 'react';\n\nconst App = () => {\n  const [editable, setEditable] = useState([\n    false,\n    { editing: false, okText: 'ok', cancelText: <span>cancel</span> },\n  ]);\n  const [content, setContent] = useState(['editable bubble 1', 'editable bubble 2']);\n\n  return (\n    <Flex vertical gap=\"small\" style={{ minHeight: 200 }}>\n      <Flex>\n        <Bubble\n          editable={editable[0]}\n          content={content[0]}\n          avatar={<Avatar icon={<UserOutlined />} />}\n          footer={\n            <Actions\n              items={[\n                {\n                  key: 'edit',\n                  icon: <EditOutlined />,\n                  label: 'edit',\n                },\n              ]}\n              onClick={({ key }) => setEditable([key === 'edit', editable[1]])}\n            />\n          }\n          onEditCancel={() => setEditable([false, editable[1]])}\n          onEditConfirm={(val) => {\n            setContent([val, content[1]]);\n            setEditable([false, editable[1]]);\n          }}\n        />\n      </Flex>\n      <Flex>\n        <Bubble\n          style={{ width: '100%' }}\n          placement=\"end\"\n          editable={editable[1]}\n          content={content[1]}\n          avatar={<Avatar icon={<UserOutlined />} />}\n          footer={\n            <Actions\n              items={[\n                {\n                  key: 'edit',\n                  icon: <EditOutlined />,\n                  label: 'edit',\n                },\n              ]}\n              onClick={({ key }) =>\n                setEditable([editable[0], { ...(editable[1] as any), editing: key === 'edit' }])\n              }\n            />\n          }\n          onEditCancel={() => setEditable([editable[0], false])}\n          onEditConfirm={(val) => {\n            setContent([content[0], val]);\n            setEditable([editable[0], { ...(editable[1] as any), editing: false }]);\n          }}\n        />\n      </Flex>\n    </Flex>\n  );\n};\n\nexport default App;\n";;
  