
  import '/Users/dupeiyi/Project/private/x/packages/x/docs/x-sdk/demos/x-chat/custom-request.tsx?watch=parent';
  export default "import type { BubbleListProps } from '@ant-design/x';\nimport { Bubble, Sender } from '@ant-design/x';\nimport { DefaultChatProvider, useXChat, XRequest, XRequestOptions } from '@ant-design/x-sdk';\nimport { Flex } from 'antd';\nimport React from 'react';\n\ninterface ChatInput {\n  query: string;\n}\n\nconst sleep = () => new Promise((resolve) => setTimeout(resolve, 1000));\n\nconst role: BubbleListProps['role'] = {\n  ai: {\n    placement: 'start',\n  },\n  local: {\n    placement: 'end',\n    contentRender(content: ChatInput) {\n      return content?.query;\n    },\n  },\n};\n\nconst App = () => {\n  const [content, setContent] = React.useState('');\n  const [provider] = React.useState(\n    new DefaultChatProvider<string, ChatInput, string>({\n      request: XRequest('https://api.example.com/chat', {\n        manual: true,\n        fetch: async (\n          _: Parameters<typeof fetch>[0],\n          options: XRequestOptions<ChatInput, string>,\n        ) => {\n          await sleep();\n          const params = options?.params;\n          return Promise.resolve(\n            new Response(JSON.stringify([`Mock success return. You said: ${params?.query}`]), {\n              headers: { 'Content-Type': 'application/json' },\n            }),\n          );\n        },\n      }),\n    }),\n  );\n\n  // Chat messages\n  const { onRequest, messages, isRequesting } = useXChat({\n    provider,\n    requestPlaceholder: 'Waiting...',\n    requestFallback: 'Mock failed return. Please try again later.',\n  });\n\n  return (\n    <Flex vertical gap=\"middle\">\n      <Bubble.List\n        role={role}\n        style={{ height: 500 }}\n        items={messages.map(({ id, message, status }) => ({\n          key: id,\n          loading: status === 'loading',\n          role: status === 'local' ? 'local' : 'ai',\n          content: message,\n        }))}\n      />\n      <Sender\n        loading={isRequesting}\n        value={content}\n        onChange={setContent}\n        onSubmit={(nextContent) => {\n          onRequest({\n            query: nextContent,\n          });\n          setContent('');\n        }}\n      />\n    </Flex>\n  );\n};\n\nexport default App;\n";;
  