
  import '/Users/dupeiyi/Project/private/x/packages/x/docs/x-sdk/demos/x-chat/deepSeek.tsx?watch=parent';
  export default "import { SyncOutlined } from '@ant-design/icons';\nimport type { BubbleListProps } from '@ant-design/x';\nimport { Bubble, Sender, Think } from '@ant-design/x';\nimport XMarkdown, { type ComponentProps } from '@ant-design/x-markdown';\nimport {\n  DeepSeekChatProvider,\n  useXChat,\n  XModelParams,\n  XModelResponse,\n  XRequest,\n} from '@ant-design/x-sdk';\nimport { Button, Divider, Flex, Tooltip } from 'antd';\nimport React from 'react';\n\n/**\n * 🔔 Please replace the BASE_URL, PATH, MODEL, API_KEY with your own values.\n */\n\nconst BASE_URL = 'https://api.x.ant.design/api/big_model_glm-4.5-flash';\n\n/**\n * 🔔 The MODEL is fixed in the current request, please replace it with your BASE_UR and MODEL\n */\n\nconst MODEL = 'glm-4.5-flash';\n\nconst ThinkComponent = React.memo((props: ComponentProps) => {\n  const [title, setTitle] = React.useState('Deep thinking...');\n  const [loading, setLoading] = React.useState(true);\n\n  React.useEffect(() => {\n    if (props.streamStatus === 'done') {\n      setTitle('Complete thinking');\n      setLoading(false);\n    }\n  }, [props.streamStatus]);\n\n  return (\n    <Think title={title} loading={loading}>\n      {props.children}\n    </Think>\n  );\n});\n\nconst role: BubbleListProps['role'] = {\n  assistant: {\n    placement: 'start',\n    contentRender(content: string) {\n      // Double '\\n' in a mark will causes markdown parse as a new paragraph, so we need to replace it with a single '\\n'\n      const newContent = content.replace('/\\n\\n/g', '<br/><br/>');\n      return (\n        <XMarkdown\n          content={newContent}\n          components={{\n            think: ThinkComponent,\n          }}\n        />\n      );\n    },\n  },\n  user: {\n    placement: 'end',\n  },\n};\n\nconst App = () => {\n  const [content, setContent] = React.useState('');\n  const [provider] = React.useState(\n    new DeepSeekChatProvider({\n      request: XRequest<XModelParams, XModelResponse>(BASE_URL, {\n        manual: true,\n        params: {\n          model: MODEL,\n          stream: true,\n        },\n      }),\n    }),\n  );\n  // Chat messages\n  const { onRequest, messages, setMessages, setMessage, isRequesting, abort, onReload } = useXChat({\n    provider,\n    requestFallback: (_, { error }) => {\n      if (error.name === 'AbortError') {\n        return {\n          content: 'Request is aborted',\n          role: 'assistant',\n        };\n      }\n      return {\n        content: 'Request failed, please try again!',\n        role: 'assistant',\n      };\n    },\n    requestPlaceholder: () => {\n      return {\n        content: 'Please wait...',\n        role: 'assistant',\n      };\n    },\n  });\n\n  const addUserMessage = () => {\n    setMessages([\n      ...messages,\n      {\n        id: Date.now(),\n        message: { role: 'user', content: 'Add a new user message' },\n        status: 'success',\n      },\n    ]);\n  };\n\n  const addAIMessage = () => {\n    setMessages([\n      ...messages,\n      {\n        id: Date.now(),\n        message: { role: 'assistant', content: 'Add a new AI response' },\n        status: 'success',\n      },\n    ]);\n  };\n\n  const addSystemMessage = () => {\n    setMessages([\n      ...messages,\n      {\n        id: Date.now(),\n        message: { role: 'system', content: 'Add a new system message' },\n        status: 'success',\n      },\n    ]);\n  };\n\n  const editLastMessage = () => {\n    const lastMessage = messages[messages.length - 1];\n    setMessage(lastMessage.id, {\n      message: { role: lastMessage.message.role, content: 'Edit a message' },\n    });\n  };\n\n  return (\n    <Flex vertical gap=\"middle\">\n      <Flex vertical gap=\"middle\">\n        <div>\n          Current status:{' '}\n          {isRequesting\n            ? 'Requesting'\n            : messages.length === 0\n              ? 'No messages yet, please enter a question and send'\n              : 'Q&A completed'}\n        </div>\n        <Flex align=\"center\" gap=\"middle\">\n          <Button disabled={!isRequesting} onClick={abort}>\n            abort\n          </Button>\n          <Button onClick={addUserMessage}>Add a user message</Button>\n          <Button onClick={addAIMessage}>Add an AI message</Button>\n          <Button onClick={addSystemMessage}>Add a system message</Button>\n          <Button disabled={!messages.length} onClick={editLastMessage}>\n            Edit the last message\n          </Button>\n        </Flex>\n      </Flex>\n      <Divider />\n      <Bubble.List\n        role={role}\n        style={{ height: 500 }}\n        items={messages.map(({ id, message }) => ({\n          key: id,\n          role: message.role,\n          content: message.content,\n          components:\n            message.role === 'assistant'\n              ? {\n                  footer: (\n                    <Tooltip title=\"Retry\">\n                      <Button\n                        size=\"small\"\n                        type=\"text\"\n                        icon={<SyncOutlined />}\n                        style={{ marginInlineEnd: 'auto' }}\n                        onClick={() =>\n                          onReload(id, {\n                            userAction: 'retry',\n                          })\n                        }\n                      />\n                    </Tooltip>\n                  ),\n                }\n              : {},\n        }))}\n      />\n      <Sender\n        loading={isRequesting}\n        value={content}\n        onCancel={() => {\n          abort();\n        }}\n        onChange={setContent}\n        onSubmit={(nextContent) => {\n          onRequest({\n            messages: [\n              {\n                role: 'user',\n                content: nextContent,\n              },\n            ],\n          });\n          setContent('');\n        }}\n      />\n    </Flex>\n  );\n};\n\nexport default App;\n";;
  